import { MutableRefObject, useContext, useEffect, useRef } from "react";
import { LayoutStateContext } from "../../../contexts/layout_state";
import { useCssStateManager } from "../../../hooks/use_css_state_manager";
import { SmartState, useSmartState } from "../../../hooks/use_smart_state";
import { lstore } from "../../../lib/localstorage";
import { misc } from "../../../lib/misc";
import { isSimpleObject } from "../../../lib/shared/options/snapshot/misc";
import { OptionsOptionCheckbox } from "../../../lib/shared/options/types";
import { prebuildData, VariantStub } from "../../../lib/prebuild-data";
import { cart, dollarPriceToCentPrice, generateCartItemInitialOptionValues, generateCartItemPriceProperties, getSimpleOptionPrice } from "../../../lib/services/cart/cart";
import { CartItem, CartItemOptionValues, OptionValue, OptionValueButton, OptionValueCheckbox, OptionValueCheckboxGroup, OptionValueProductSelect, OptionValueSelect, OptionValueVariantSelect } from "../../../lib/services/cart/types";
import { ConfigProduct } from "../../../scripts/pre-build/types";
import StarRating from "../../common/star_rating";
import AddToCart from "./add-to-cart";
import ButtonsOption from "./options/buttons";
import CheckboxOption from "./options/checkbox";
import CheckboxGroupOption from "./options/checkbox-group";
import ColorPickerOption from "./options/color-picker";
import OptionTitle from "./options/commons/title";
import MultiSelectOption from "./options/multi-select";
import SelectComponent from "./options/select";
import SelectWithQuantity from "./options/select-with-quantity";
import PriceTopSlider from "./price-top-slider";
import PriceUpdate from "./price-update";
import ProductHeader from "../../../components/pages/product/header";
import { WindowSizeContext } from "../../../contexts/window_size";
import { pubSubRequest } from "../../../hooks/use_pub_sub_request";
import { AddToCartActions, OptionsHaveAddons } from "../../../pages/collections/[id]/products/[product_id]";

export type OptionsProps = {
    editCartItem: SmartState<CartItem>;
    product: ConfigProduct;
    galleryHeight: number;
    onSlideShowMatch?: (value: string,bypassSlideshowAnimation?: boolean) => void;
    currentPrice: SmartState<number>;
    addToCartRef?: MutableRefObject<HTMLDivElement>;
    alreadyAddedToCart: SmartState<boolean>;
    alreadyAlreadyAddedToCart: SmartState<boolean>;
    addToCartEnabled: SmartState<boolean>;
    addToCartActionsRef: MutableRefObject<AddToCartActions>;
    topSectionRef: MutableRefObject<HTMLDivElement>;
    titleBadgeRef: MutableRefObject<HTMLDivElement>;
    innerWrapperRef: MutableRefObject<HTMLDivElement>;
    hasAddOns: SmartState<OptionsHaveAddons>;
    descriptionHasTabs: boolean;
};

export default function Options({
        editCartItem,
        product,
        addToCartActionsRef,
        addToCartEnabled,
        galleryHeight,
        onSlideShowMatch = () => {},
        currentPrice,
        addToCartRef,
        alreadyAddedToCart,
        alreadyAlreadyAddedToCart,
        topSectionRef,
        titleBadgeRef,
        innerWrapperRef,
        hasAddOns,
        descriptionHasTabs
    }:OptionsProps)
{
    const optionsCompiler = prebuildData.getOptionsCompiler();
    
    const { sideCart,page,scalePixelToRem,mobileDetect,mobileDetectUseEffect,pageRef,productPageLoadCounter } = useContext(LayoutStateContext);

    const originalProductId = product.id.toString();
    
    const productOptions = optionsCompiler.getProductOptions(product.id);    
    const { options } = productOptions;

    if (product.visibility==="tease") options.length = 0;

    const includedCheckboxOptions = options.filter((option) => ((option.type==="checkbox") && (option.included))) as OptionsOptionCheckbox[];
    const nonIncludedCheckboxOptions = options.filter((option) => ((option.type==="checkbox") && (!option.included))) as OptionsOptionCheckbox[];
    const hasIncluded = (includedCheckboxOptions.length>0);
    const hasNonIncluded = (nonIncludedCheckboxOptions.length>0);

    let firstNonIncludedCheckboxIndex = -1;
    let lastNonIncludedCheckboxIndex = -1;

    options.forEach((option,index) => {
        if ((option.type==="checkbox") && (!option.included)) {
            if (firstNonIncludedCheckboxIndex===-1) firstNonIncludedCheckboxIndex = index;
            lastNonIncludedCheckboxIndex = index;   
        }
    });

    useEffect(() => {
        hasAddOns.value = (firstNonIncludedCheckboxIndex!==-1)?"yes":"no";
    },[]);

    const { isSmallDesktopScreen: isMobileActive,smallDesktopWidthCutoff: mobileCutoff } = mobileDetect;

    const sliderState = useSmartState(0);
    const sliderHeightTransitionDuration = useSmartState(0);
    const addToCartDisabledBypass = useSmartState(false);

    const cssStateManager = useCssStateManager(["container"]);
    cssStateManager.useProperty(sliderState.value===0,"slide-height-zero");
    cssStateManager.useProperty(sliderState.value===-1,"slide-height-auto");    

    const show = cssStateManager.useState(false,"show");
    cssStateManager.useProperty(addToCartDisabledBypass.value && (sliderState.value===0),"add-to-cart-disabled-bypass");    

    const lsDigestKey = `pd_${product.id}`;

    const loadFromSavedOptionValues = useSmartState<CartItemOptionValues>({});

    const productSelectProductListCacheRef = useRef<{[id: string]: ConfigProduct[]}>({});
    const { current: productSelectProductListCache } = productSelectProductListCacheRef;
    const productSelectItemCaptionFnCacheRef = useRef<{[id: string]: any}>({});
    const { current: productSelectItemCaptionFnCache } = productSelectItemCaptionFnCacheRef;
    const variantSelectItemsFnCacheRef = useRef<{[id: string]: any}>({});
    const { current: variantSelectItemsFnCache } = variantSelectItemsFnCacheRef;
    const variantSelectItemCaptionFnCacheRef = useRef<{[id: string]: any}>({});
    const { current: variantSelectItemCaptionFnCache } = variantSelectItemCaptionFnCacheRef;
    const checkboxProductFnCacheRef = useRef<{[id: string]: any}>({});
    const { current: checkboxProductFnCache } = checkboxProductFnCacheRef;

    // const enabledCount = useSmartState(1);
    const enabledCount = useSmartState(100);    
    const isFirstRef = useRef(0);

    const cartItemRef = useRef<CartItem>();
    const optionIdListRef = useRef<string[]>();
    const requiredOptionsHashRef = useRef<{[id: string]: true}>();
    const digestLoadedRef = useRef(false);

    let enabledOptionsHashInit: {[id: string]: boolean} = {};

    const isOpenRef = useRef(false);

    const genEnabledOptionsHash = () => {
        const ret: {[id: string]: boolean} = {};
        const { current: optionIdList } = optionIdListRef;
        const { current: requiredOptionsHash } = requiredOptionsHashRef;

        let enabledValue = true;
        let isOpen = true;

        optionIdList.forEach((optionId) => {
            ret[optionId] = enabledValue;
            isOpen = isOpen && enabledValue;
            if (requiredOptionsHash[optionId]) {
                enabledValue = false;
            }
        });

        isOpenRef.current = isOpen;

        return ret;
    };

    const includedId = "--included--";
    const addToCartId = "--add-to-cart--";    

    // const alreadyAddedToCart = useSmartState(false);
    // const alreadyAlreadyAddedToCart = useSmartState(true);
    const currentCartItemDigestRef = useRef("");

    useEffect(() => {
        cart.deletionSubscribe((digest) => {
            if (currentCartItemDigestRef.current===digest) {
                alreadyAlreadyAddedToCart.value = false;
                alreadyAddedToCart.value = false;
            }
        });
    },[]);    

    const updateAlreadyAddedToCart = (cartItem: CartItem,cb: (alreadyAddedToCartValue: boolean) => void = () => {}) => {
        cart.runWhenLoaded(() => {
            const {uuid: cartItemId,digest} = cart.getItemIdByDigest(cartItem);
            currentCartItemDigestRef.current = digest;
            alreadyAlreadyAddedToCart.value = true;
            const alreadyAddedToCartValue = (cartItemId!=="");
            alreadyAddedToCart.value = alreadyAddedToCartValue;  
            cb(alreadyAddedToCartValue);  
        });
    };

    const saveItemCartToLocalStorage = () => {        
        if (cart.editOrder) return;
        if (!digestLoadedRef.current) return;
        const savedOptionValues: CartItemOptionValues = {};
        const acceptedKeys: {[key: string]: true} = {};
        options.forEach((option,index) => {
            const { id } = option;
            if (!(((option.type==="buttons") || (option.type==="checkbox") || (option.type==="variant-select")) && (option.cartLink))) {
                acceptedKeys[id] = true;
            }
            // if (firstNonIncludedCheckboxIndex!==-1) {
            //     if (index<firstNonIncludedCheckboxIndex) {
            //         acceptedKeys[id] = true;    
            //     }
            // }
            // else {
            //     acceptedKeys[id] = true;
            // }
        });
        
        Object.keys(cartItem.optionValues)
            .filter((key) => acceptedKeys[key] && !cartItem.optionValues[key].defaultSet)
            .forEach((key) => savedOptionValues[key] = cartItem.optionValues[key]);           
        lstore.set(lsDigestKey,savedOptionValues);
        // console.log("im here sure111",lsDigestKey,savedOptionValues,lstore.get(lsDigestKey));
    };

    if (!cartItemRef.current) {

        const productId = product.id.toString();         
        const cartItem: CartItem = {
            activatedOptionsMap: {},
            optionValues: generateCartItemInitialOptionValues(productId),
            productId: productId,
            quantity: 1,
            quantityDisplay: "1",
            uuid: misc.genUUID(),
            version: 1
        };
        cartItemRef.current = cartItem;

        //---

        const optionIdList: string[] = [];
        const requiredOptionsHash: {[id: string]: true} = {};
        const { options } = optionsCompiler.getProductOptions(product.id);
        options.forEach((option) => {
            if (option.type==="buttons") {
                if (option.items.filter((item) => item.selected).length===0) {
                    requiredOptionsHash[option.id] = true;
                }                
            }
            else if ((option.type==="product-select") && (option.multiSelect)) {
                requiredOptionsHash[option.id] = true;
            }
            if ((option.type!=="checkbox") || (!option.included)) {
                optionIdList.push(option.id);
            }
        });
        optionIdList.push(includedId,addToCartId);

        optionIdListRef.current = optionIdList;
        requiredOptionsHashRef.current = requiredOptionsHash;

        enabledOptionsHashInit = genEnabledOptionsHash();
    }

    const processLoadedDigestForMobileSliderHeightState = (digest: CartItemOptionValues) => {
        const { options } = optionsCompiler.getProductOptions(product.id);
        let isOpen = true;
        options.forEach((option) => {
            if (!isOpen) return;

            if (option.type==="buttons") {
                if (option.items.filter((item) => item.selected).length===0) {
                    if (!digest[option.id]) {
                        return (isOpen = false);
                    }
                }                
            }
            else if ((option.type==="product-select") && (option.multiSelect)) {
                const digestValue = digest[option.id];
                if ((!digestValue) || ((digestValue.type==="product-select") && (digestValue.ids.length<option.multiSelect.min))) {
                    return (isOpen = false);
                }
            }
        });
        if (isOpen) {
            const tmpCartItem: CartItem = {
                productId: product.id.toString(),
                quantity: 1,
                quantityDisplay: "1",
                uuid: "",
                version: 0,
                activatedOptionsMap: {},
                optionValues: {...digest}
            };
            cart.runWhenLoaded(() => {                
                const {uuid: cartItemId} = cart.getItemIdByDigest(tmpCartItem);
                if (cartItemId!=="") {
                    if (options.length>0) {
                        sliderState.value = -1;
                    }                    
                }
            });
        }
        else {
            addToCartDisabledBypass.value = true;
        }
    };

    const currentlyEditingUuid = useRef(cart.editCartItem?cart.editCartItem.uuid:"");

    mobileDetectUseEffect(() => {        
        const removeDeadVariants = (digest: CartItemOptionValues) => {
            Object.keys(digest).forEach((key) => {
                const optionValue = digest[key];
                if (optionValue.type==="variant-select") {
                    if (!prebuildData.getVariantStub(optionValue.id)) {
                        delete digest[key];
                    }                    
                }
            });
            return digest;
        };

        let digest = cart.editOrder?null:lstore.get<CartItemOptionValues>(lsDigestKey);
        digestLoadedRef.current = true;
        if ((cart.editCartItem) && (cart.editCartItem.productId===product.id.toString())) {            
            digest = cart.editCartItem.optionValues;
            editCartItem.value = cart.editCartItem;
            cart.editCartItem = null;            
        }
        if ((digest) && (Object.keys(digest).length>0)) {
            if (isMobileActive.current) processLoadedDigestForMobileSliderHeightState(digest);
            loadFromSavedOptionValues.value = removeDeadVariants(digest);
        }
        else {            
            if (isOpenRef.current) {
                // updateAlreadyAddedToCart(cartItem,(alreadyAddedToCartValue) => {
                //     if ((isMobileActive.current) && (alreadyAddedToCartValue)) {
                //         if (options.length>0) {
                //             sliderState.value = -1;
                //         }                    
                //     }
                // });
            }    
            else if (isMobileActive.current) {
                addToCartDisabledBypass.value = true;
            }
        }
        show.value = true;
    },[],"small_desktop");


    const enabledOptionsHash = useSmartState(enabledOptionsHashInit);

    const { current: cartItem } = cartItemRef;
    const { current: requiredOptionsHash } = requiredOptionsHashRef;

    const scrollToOption = (optionId: string) => {
        if (optionsSliderRef.current) {
            const optionElem = optionsSliderRef.current.querySelector(`.opt_${optionId}`) as HTMLDivElement;
            if (optionElem) {
                if (pageRef.current) {
                    const pageElem = pageRef.current;
                    const offsetTop = optionElem.offsetTop+(optionElem.offsetParent as HTMLDivElement).offsetTop;
                    const midpointOffsetTop = offsetTop+(optionElem.offsetHeight/2);
                    const midpointOffsetTopFromScreen = midpointOffsetTop-pageElem.scrollTop;
                    const target = window.innerHeight/2;
                    const delta = target-midpointOffsetTopFromScreen;
                    const startScrollTop = pageElem.scrollTop;
                    const durationPer100 = 375;                
                    const duration = (Math.abs(scalePixelToRem(delta))/100)*durationPer100;
                    let lastSetScroll = -1;
                    setTimeout(() => {
                        const timestamp = (new Date()).getTime();
                        // pageElem.scrollTo({top: pageElem.scrollTop-delta,behavior: "smooth"});                
                        // const easeFunc = (t: number) => 1 - Math.cos((t * Math.PI) / 2);
                        const easeFunc = (x: number) => 1 - Math.pow(1 - x, 3);
                        const next = () => {
                            const timespan = Math.min((new Date()).getTime()-timestamp,duration);                 
                            if ((lastSetScroll!==-1) && (Math.abs(pageElem.scrollTop-lastSetScroll)>=1)) return;
                            lastSetScroll = pageElem.scrollTop = startScrollTop-((easeFunc(timespan/duration))*delta);
                            if (timespan<duration) requestAnimationFrame(next);
                        };
                        requestAnimationFrame(next);    
                    },450);    
                }
            }
        }
    };

    const refreshPrice = (incVersion = false) => {
        if (incVersion) cartItem.version++;
        const { price } = generateCartItemPriceProperties(cartItem,{enforceLimitOnePerOrder: true,ignoreUuid: currentlyEditingUuid.current});
        currentPrice.value = price;
    };

    const updateItemCartOptionValue = (id: string,value: OptionValue | null,auto?: boolean) => {
        if (value!==null) {
            cartItem.optionValues[id] = value;
            if (requiredOptionsHash[id]) {
                const { options } = optionsCompiler.getProductOptions(product.id);
                const option = options.filter((option) => option.id===id)[0];
                if (option) {
                    let found = false;
                    if (option.type==="buttons") {
                        found = true;
                    }
                    else if ((option.type==="product-select") && (option.multiSelect) && (value.type==="product-select") && (value.ids.length>=option.multiSelect.min)) {
                        found = true;
                    }
                    if (found) {
                        const beforeEnabledHash = genEnabledOptionsHash();
                        delete requiredOptionsHash[id];
                        const afterEnabledHash = genEnabledOptionsHash();
                        let newlyEnabledOptionId = "";
                        if ((isMobileActive.current) && (!auto)) {
                            Object.keys(afterEnabledHash).forEach((optionId) => {
                                if ((newlyEnabledOptionId!=="") || (optionId===includedId)) return;
                                if ((afterEnabledHash[optionId]) && (!beforeEnabledHash[optionId])) {
                                    newlyEnabledOptionId = optionId;
                                }
                            });
                            if (newlyEnabledOptionId) {
                                scrollToOption(newlyEnabledOptionId);
                            }
                        }
                        enabledOptionsHash.value = afterEnabledHash;        
                    }
                }
            }
        }
        else {
            delete cartItem.optionValues[id];
        }        

        refreshPrice(true);

        saveItemCartToLocalStorage();

        if (isOpenRef.current) {
            // updateAlreadyAddedToCart(cartItem);
        }
    };

    useEffect(() => {
        refreshPrice();
    },[]);

    const slideFrameRef = useRef<HTMLDivElement>();

    const { dollar,cents } = misc.splitPriceForDisplay(currentPrice.value);    

    const calculateSliderHeightTransitionDuration = () => {
        if (slideFrameRef.current) {
            const durationPer100 = 0.165;
            const targetHeight = scalePixelToRem(slideFrameRef.current.offsetHeight)-20;
            return (targetHeight/100)*durationPer100;
        }
        else {
            return 0;
        }
    };

    const optionsSliderRef = useRef<HTMLDivElement>();

    const optionsSliderTransitionEndHandler = (target: HTMLDivElement,propertyName: string) => {
        if ((target===optionsSliderRef.current) && (propertyName==="height") && (sliderState.value>0)) {
            sliderState.value = -1;
            if (tempDisableAddToCartRef.current) {
                tempDisableAddToCartRef.current = false;
            }
        }
    };

    const tempDisableAddToCartRef = useRef(false);

    // useEffect(() => {
    //     if ((alreadyAddedToCart.value) && (tempDisableAddToCartRef.current)) {
    //         tempDisableAddToCartRef.current = false;
    //     }
    // },[alreadyAddedToCart.value]);

    useEffect(() => {
        if (tempDisableAddToCartRef.current) {
            tempDisableAddToCartRef.current = false;
        }
    },[tempDisableAddToCartRef.current]);

    // console.log("product.id",product.id);

    useEffect(() => {
        addToCartEnabled.value = !!enabledOptionsHash.value[addToCartId];
    },[!!enabledOptionsHash.value[addToCartId]])

    const addToCartClickHandler = (quantity: number) => {
        if (tempDisableAddToCartRef.current) return;
        if ((isMobileActive.current) && (sliderState.value===0) && (options.length>0)) {
            if (slideFrameRef.current) {
                tempDisableAddToCartRef.current = true;
                sliderHeightTransitionDuration.value = calculateSliderHeightTransitionDuration();                
                sliderState.value = scalePixelToRem(slideFrameRef.current.offsetHeight)-20;
                const firstOptionId = options?.[0]?.id;
                if (firstOptionId) {
                    scrollToOption(firstOptionId);
                }
            }
        }
        else {
            tempDisableAddToCartRef.current = true;
            const { highlightIds,flashIds } = cart.addItem({...cartItem,quantity,quantityDisplay: quantity.toString()});
            sideCart.open({highlightIds,flashIds});
            refreshPrice(true);

            // const setAlreadyAdded = () => {
            //     alreadyAddedToCart.value = true;
            //     alreadyAlreadyAddedToCart.value = false;    
            // };

            // if (isMobileActive.current) {
            //     setTimeout(setAlreadyAdded,300);
            // }
            // else {
            //     setAlreadyAdded();
            // }
        }
    };

    const addToCartViewInCartHandler = () => {
        sideCart.open({flashIds: [cart.getItemIdByDigest(cartItem).uuid]});
    };

    const addToCartSaveClickHandler = () => {
        if (tempDisableAddToCartRef.current) return;
        if (!editCartItem.value) return;
        const id = editCartItem.value.uuid;
        cart.updateCartItem(id,cartItem);        
        editCartItem.value = null;
        currentlyEditingUuid.current = "";
        sideCart.open({flashIds: [id]});
        refreshPrice(true);

        productPageLoadCounter.value++;


        // const setAlreadyAdded = () => {
        //     alreadyAddedToCart.value = true;
        //     alreadyAlreadyAddedToCart.value = false;    
        // };

        // if (isMobileActive.current) {
        //     setTimeout(setAlreadyAdded,300);
        // }
        // else {
        //     setAlreadyAdded();
        // }
    };

    addToCartActionsRef.current.onClick = addToCartClickHandler;
    addToCartActionsRef.current.onViewInCart = addToCartViewInCartHandler;
    addToCartActionsRef.current.onSaveClick = addToCartSaveClickHandler;
    

    let addedOptionCount = 0;

    const teaseCaption = ((product.visibility==="tease") && (product.teaseCaption))?product.teaseCaption:"";
    let foundFirstCartLink = false;

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <div className="inner-wrapper" ref={innerWrapperRef}>
                    <div className="desktop-header">
                        <ProductHeader product={product} currentPrice={currentPrice} />
                    </div>
                    <div 
                        className="options-slider" 
                        style={{
                            ...((sliderState.value>0)?{height: `${sliderState.value}rem`}:{}),
                            ...((sliderState.value>0)?{transition: `height ${sliderHeightTransitionDuration.value}s`}:{})
                        }}
                        onTransitionEnd={(e) => optionsSliderTransitionEndHandler(e.target as HTMLDivElement,e.propertyName)}
                        ref={optionsSliderRef}
                    >
                        <div className="options-slider-frame" ref={slideFrameRef}>
                    {options.map((option,index) => {
                        const isFirstCartLink = (() => {
                            if (((option.type==="buttons") || (option.type==="checkbox") || (option.type==="variant-select")) && (option.cartLink)) {
                                if (!foundFirstCartLink) {
                                    foundFirstCartLink = true;
                                    return true;
                                }
                            }
                            return false;
                        })();

                        const enabledIndex = index;
                        //"Choose your color"
                        if (option.type==="buttons") {
                            const { caption,component,items,minWidth,colorPickerSize,quantity,cartLink } = option;
                            if (component==="color-picker") {
                                let initialSelectedItemIndex = -1;
                                for(let i=0;i<items.length;i++) {
                                    if (items[i].selected) {
                                        initialSelectedItemIndex = i;
                                        break;
                                    }
                                }
                                addedOptionCount++;

                                const loadFromSavedValue = (() => {
                                    if (items.length===1) {
                                        return {
                                            type: "buttons",
                                            id: items[0].id
                                        };
                                    }
                                    return loadFromSavedOptionValues.value[option.id];
                                })();

                                return (
                                    <div key={index} className={`option opt_${option.id} ${(!enabledOptionsHash.value[option.id])?"disabled":""}`}>
                                        <ColorPickerOption
                                            onSlideShowMatch={onSlideShowMatch}
                                            title={caption}
                                            colors={items.map(({id,color,caption,slideshowMatch}) => ({
                                                    id,
                                                    color: color || "#000000",
                                                    caption,
                                                    slideshowMatch
                                                }))}
                                            size={colorPickerSize}
                                            initialSelectedItemIndex={initialSelectedItemIndex}                                        
                                            onValue={(value,auto = false) => {                                                
                                                updateItemCartOptionValue(option.id,value,auto);
                                            }}
                                            loadFromSavedValue={loadFromSavedValue as OptionValueButton}
                                            {...(cartLink?{price: getSimpleOptionPrice(originalProductId,option.id)}:{})}
                                        />                                  
                                        <div className="blocker"></div>
                                    </div>
                                );
                            }
                            else {
                                addedOptionCount++;

                                const loadFromSavedValue = (() => {
                                    if (items.length===1) {
                                        return {
                                            type: "buttons",
                                            id: items[0].id
                                        };
                                    }
                                    return loadFromSavedOptionValues.value[option.id];
                                })();
                                
                                return (
                                    <div key={index} className={`option opt_${option.id} ${(!enabledOptionsHash.value[option.id])?"disabled":""}`}>
                                        <ButtonsOption
                                            onSlideShowMatch={onSlideShowMatch} 
                                            title={caption}
                                            minWidth={minWidth}
                                            buttons={items.map(({caption,value,id,slideshowMatch}) => ({
                                                caption,
                                                id,
                                                slideshowMatch,
                                                ...(isSimpleObject(value)?{
                                                    custom: true,
                                                    customRange: {
                                                        min: value.range?.from,
                                                        max: value.range?.to,
                                                        decimalUnit: (value.type==="number")?Math.round(value.unit*10):0
                                                    },
                                                    customDisplay: value.display,
                                                    customPlaceholder: value.placeholder
                                                }:{})
                                            }))}
                                            onValue={(value,auto = false) => {
                                                updateItemCartOptionValue(option.id,value,auto);
                                            }}
                                            loadFromSavedValue={loadFromSavedValue as OptionValueButton}
                                            quantity={quantity}
                                        />
                                        <div className="blocker"></div>
                                    </div>
                                );
                            }                        
                        }
                        else if (option.type==="product-select") {
                            const { caption,subCaption,multiSelect,filters,sort,id,itemCaption } = option;                        
                            const products = (productSelectProductListCache[id] = productSelectProductListCache[id] || prebuildData.getFilteredProductList(filters,sort));
                            
                            const itemCaptionFn = productSelectItemCaptionFnCache[id] = productSelectItemCaptionFnCache[id] || prebuildData.evalInContext(itemCaption);
                            
                            if (multiSelect) {
                                addedOptionCount++;
                                return (
                                    <div key={index} className={`option opt_${option.id} ${(!enabledOptionsHash.value[option.id])?"disabled":""}`}>
                                        <MultiSelectOption
                                            title={caption}
                                            subTitle={subCaption || ""}
                                            input={{
                                                captions: multiSelect.captions,
                                                min: multiSelect.min,
                                                max: multiSelect.max,
                                                entries: products.map(({id}) => {
                                                    const caption = itemCaptionFn({product: prebuildData.getProductStub(id.toString())});                                                
                                                    return {
                                                        caption,
                                                        value: id.toString()
                                                    };
                                                }),
                                                showAll: multiSelect.showAll
                                            }}
                                            onValue={(value,auto = false) => {
                                                updateItemCartOptionValue(option.id,value,auto);
                                            }}
                                            loadFromSavedValue={loadFromSavedOptionValues.value[option.id] as OptionValueProductSelect}                                        
                                        />
                                        <div className="blocker"></div>
                                    </div>
                                );    
                            }
                            else {
                                return "";
                            }
                        }
                        else if (option.type==="variant-select") {
                            const { caption,multiple,unselectedCaption,items,itemCaption,id,description,limitOnePerOrder } = option;
                            if (limitOnePerOrder) {                            
                                if (cart.hasDealAlready(product.id.toString(),id,currentlyEditingUuid.current)) {
                                    delete cartItem.optionValues[option.id];
                                    return "";
                                }                            
                            }                                                        
                            const itemsFn = (variantSelectItemsFnCache[id] = variantSelectItemsFnCache[id] || prebuildData.evalInContext(items.split('.deal["No Deal"]').join('.deal?.["No Deal"]')));
                            // console.log("itemsFn",itemsFn.toString());
                            const variants = (itemsFn() as VariantStub[]).filter(({custom}) => !custom);

                            const price = (() => {
                                console.log("option.cartLink",option.cartLink);
                                if ((option.cartLink) && (variants.length>0)) {                                    
                                    if (option.cartLink.type==="normal") {
                                        const ret = prebuildData.getVariantHash()[variants[0].id]?.variant?.price;                                    
                                        return (typeof ret==="number")?dollarPriceToCentPrice(ret):undefined;    
                                    }
                                    else /* if (option.cartLink.type==="deal") */ {
                                        const ret = prebuildData.evalInContext(option.cartLink.price)();
                                        return (typeof ret==="number")?dollarPriceToCentPrice(ret):undefined;    
                                    }
                                }
                            })();

                            console.log("im here price",price);

                            const classNameAddons: string[] = [];
                            if (isFirstCartLink) classNameAddons.push("first-cartlink");
                            
                            const itemCaptionFn = (variantSelectItemCaptionFnCache[id] = variantSelectItemCaptionFnCache[id] || prebuildData.evalInContext(itemCaption));

                            if (multiple) {
                                addedOptionCount++;
                                return (
                                    <div key={index} className={`option ${classNameAddons.join(" ")} option_variant_select opt_${option.id} ${(!enabledOptionsHash.value[option.id])?"disabled":""}`}>
                                        <SelectWithQuantity 
                                            title={caption}
                                            unselectedCaption={unselectedCaption?unselectedCaption:""}
                                            entries={variants.map(({id: variantId}) => {
                                                const stubs = prebuildData.getVariantAndProductStub(variantId);
                                                return {
                                                    caption: stubs?itemCaptionFn(stubs):"",
                                                    value: variantId
                                                };
                                            })}
                                            onValue={(value) => {
                                                updateItemCartOptionValue(option.id,value);
                                            }}
                                            loadFromSavedValue={loadFromSavedOptionValues.value[option.id] as OptionValueVariantSelect}
                                            price={price}
                                        />
                                        <div className="blocker"></div>
                                    </div>
                                );
                            }
                            else {        
                                addedOptionCount++;               
                                return (
                                    <div key={index} className={`option ${classNameAddons.join(" ")} option_variant_select opt_${option.id} ${(!enabledOptionsHash.value[option.id])?"disabled":""}`}>
                                        <SelectComponent
                                            title={caption}
                                            unselectedCaption={unselectedCaption?unselectedCaption:""}
                                            removable={!!unselectedCaption}
                                            description={description}
                                            entries={variants.map(({id: variantId}) => {
                                                const stubs = prebuildData.getVariantAndProductStub(variantId);
                                                return {
                                                    caption: stubs?itemCaptionFn(stubs):"",
                                                    value: variantId
                                                };
                                            })}
                                            valueType="variant-select"
                                            onValue={(value) => {
                                                updateItemCartOptionValue(option.id,value);
                                            }}
                                            price={price}
                                            loadFromSavedValue={loadFromSavedOptionValues.value[option.id] as OptionValueVariantSelect}
                                        />
                                        <div className="blocker"></div>
                                    </div>
                                );
                            }
                        }
                        else if (option.type==="checkbox") {
                            const { included,caption,product,description,id,cartLink } = option;
                            
                            if (!included) {
                                let productFn = () => ({id: ""});
                                if (product) {                                
                                    productFn = (checkboxProductFnCache[id] = checkboxProductFnCache[id] || prebuildData.evalInContext(product));
                                }
                                const productId = productFn()?.id || "";
                                const classNameAddons: string[] = [];
                                // if ((firstNonIncludedCheckboxIndex===index) && (addedOptionCount>0)) classNameAddons.push("first-cartlink");
                                // if (lastNonIncludedCheckboxIndex===index) classNameAddons.push("last-checkbox");
                                if (isFirstCartLink) classNameAddons.push("first-cartlink");
                                addedOptionCount++;
                                return (

                                    <div key={index} className={`option ${classNameAddons.join(" ")} option_checkbox opt_${option.id} ${(!enabledOptionsHash.value[option.id])?"disabled":""}`}>
                                        <CheckboxOption 
                                            title={caption}
                                            productId={productId}
                                            description={description}
                                            onValue={(value) => {
                                                updateItemCartOptionValue(option.id,value);
                                            }}
                                            loadFromSavedValue={loadFromSavedOptionValues.value[option.id] as OptionValueCheckbox}
                                            {...(cartLink?{price: getSimpleOptionPrice(originalProductId,id)}:{})}
                                        />
                                        <div className="blocker"></div>
                                    </div>
                                );    
                            }
                            else {
                                return "";
                            }
                        }
                        else if (option.type==="select") {
                            const { caption,items } = option;
                            addedOptionCount++;
                            return (
                                <div key={index} className={`option opt_${option.id} ${(!enabledOptionsHash.value[option.id])?"disabled":""}`}>
                                    <SelectComponent 
                                        title={caption}
                                        entries={items.map(({caption,id}) => ({caption,value: id}))}
                                        valueType="select"
                                        onValue={(value) => {
                                            updateItemCartOptionValue(option.id,value);
                                        }}
                                        loadFromSavedValue={loadFromSavedOptionValues.value[option.id] as OptionValueSelect}
                                    />        
                                    <div className="blocker"></div>
                                </div>
                            );
                        }
                        else if (option.type==="checkbox-group") {
                            const { caption,items } = option;
                            addedOptionCount++;
                            return (
                                <div key={index} className={`option opt_${option.id} ${(!enabledOptionsHash.value[option.id])?"disabled":""}`}>
                                    <CheckboxGroupOption 
                                        title={caption}
                                        entries={items.map(({caption,id}) => {
                                            const res = /^([\s\S]*)\(([\s\S]*)\)\s*$/.exec(caption);                                        
                                            if (res) {                                            
                                                const caption = res[1].trim();
                                                const subCaption = res[2].trim();
                                                return {
                                                    caption,
                                                    subCaption,
                                                    value: id
                                                };
                                            }
                                            else {
                                                return {caption,value: id};
                                            }                                        
                                        })}
                                        onValue={(value) => {
                                            updateItemCartOptionValue(option.id,value);
                                        }}
                                        loadFromSavedValue={loadFromSavedOptionValues.value[option.id] as OptionValueCheckboxGroup}
                                    />
                                    <div className="blocker"></div>
                                </div>
                            );
                        }
                        else {
                            return "";
                        }
                    })}
                    {(() => {                    
                        if (hasIncluded) {
                            return "";
                            
                            return (
                                <div className={`option ${(!enabledOptionsHash.value[includedId])?"disabled":""}`}>
                                    <OptionTitle caption="Included" />
                                    {includedCheckboxOptions.map(({caption,description,product,id},index) => {
                                        let productFn = () => ({id: ""});
                                        if (product) {                                
                                            productFn = (checkboxProductFnCache[id] = checkboxProductFnCache[id] || prebuildData.evalInContext(product));
                                        }
                                        const productId = productFn()?.id || "";
            
                                        return (
                                            <div key={index} className={`included-checkbox-wrapper ${(index===includedCheckboxOptions.length-1)?"is-last":""}`}>
                                                <CheckboxOption                                                 
                                                    title={caption}
                                                    titleStyle={"internal"}
                                                    productId={productId}
                                                    description={description}
                                                    included      
                                                />
                                            </div>
                                    )})}
                                </div>        
                            );
                        }
                        if (hasNonIncluded) {

                        }
                    })()}
                        </div>
                    </div>                
                    <div className={`option  opt_${addToCartId} ${(!addToCartEnabled.value)?"disabled":""}`}>
                        <AddToCart onClick={addToCartClickHandler} 
                            onViewInCart={addToCartViewInCartHandler}
                            added={alreadyAddedToCart.value}
                            already={alreadyAlreadyAddedToCart.value}
                            containerRef={addToCartRef}
                            descriptionHasTabs={descriptionHasTabs}
                            sliderIsOpen={sliderState.value===-1}
                            noticeText={productOptions.vars["notice"]}
                            teaseCaption={teaseCaption}
                        />
                        <div className="blocker"></div>
                    </div>     
                </div>                                     
            </div>
            <PriceTopSlider 
                productId={product.id.toString()}
                title={product.caption} 
                price={currentPrice.value} 
                topSectionRef={topSectionRef}
                titleBadgeRef={titleBadgeRef}
            />
            <style jsx>{`
            .container {
                position: relative;
                padding-top: 52rem;
                opacity: 0;
                transition: opacity 1.3s;
            }
            /*
            .option_checkbox {
                --outer: 32rem;
                background-color: #f5f5f5;
                margin-left: calc(var(--outer) * -1);
                margin-right: calc(var(--outer) * -1);
                padding-left: var(--outer);
                padding-right: var(--outer);
            } 
            */           
            .container.show {
                opacity: 1;
            }
            .option-page {
                display: flex;
                flex-direction: column;
                justify-content: center;
            }
            .option {
                padding-top: 30rem;
                position: relative;
                opacity: 1;
                transition: opacity 0.3s;
            }
            .option_checkbox.first-cartlink,.option_variant_select.first-cartlink {
                padding-top: 30rem;
                margin-top: 30rem;
                border-top: 1rem solid #ddd;
            }

            /*
            .option_checkbox.first-cartlink {
                padding-top: 20rem;
                margin-top: 20rem;
            }
            .option_checkbox.last-checkbox {
                padding-bottom: 40rem;
            }
            */
            .option .blocker {
                position: absolute;
                left: 0rem;
                right: 0rem;
                top: 0rem;
                bottom: 0rem;
                display: none;
            }
            .option.disabled .blocker {
                display: block;
            }
            .option.disabled {
                opacity: 0.32;
            }
            .top-section {
                /*
                position: absolute;                
                top: 0rem;
                */
            }
            .top-option-frame {
            }
            .title {
                font-size: 24rem;
                margin-top: 6rem;
                letter-spacing: 1rem;            
            }                        
            .price {
                margin-top: 6rem;
                font-size: 27rem;
                font-weight: 600;                
            }
            .sup {
                font-size: 14rem;
                position: relative;
                top: -9rem;            
            }
            .sup.cents {
                left:1.5rem;
            }
            .included-checkbox-wrapper {
                margin-bottom: 15rem;
            }
            .included-checkbox-wrapper.is-last {
                margin-bottom: 0rem;
            }
            .price-update {
                display: inline-block;
                position: relative;
                top: -5rem;
                margin-left: 12rem;
            }

            .desktop-header {
                display: none;
            }

            .options-slider {
                margin-top: -52rem;
            }

            .option.opt_--add-to-cart-- {
                display: none;
            }

            @media screen and (max-width: ${mobileCutoff}px) {
                .option.opt_--add-to-cart-- {
                    display: block;
                }

                .desktop-header {
                    display: none;
                }

                .container {
                    padding-top: 0rem;
                }

                .options-slider {
                    overflow: hidden;                    
                    --padding-hori: 30rem;
                    margin-left: calc(var(--padding-hori) * -1);
                    margin-right: calc(var(--padding-hori) * -1);
                    margin-bottom: -35rem;
                    margin-top: 20rem;
                }

                .options-slider-frame {
                    /* background-color: #f7f7f7; */
                    background-color: #f3f3f3;
                    padding: 13rem var(--padding-hori);
                    padding-bottom: 37rem;
                    margin-top: -20rem;
                }

                .slide-height-zero .options-slider {
                    height: 0rem;
                }

                .slide-height-auto .options-slider {
                    height: auto;
                }
                .add-to-cart-disabled-bypass .option.opt_--add-to-cart--.disabled .blocker {
                    display: none;
                }

                .add-to-cart-disabled-bypass .option.opt_--add-to-cart--.disabled {
                    opacity: 1;
                }
            }
            `}</style>
        </>
    );
}