import { useEffect } from "react";

export type PubSubRequestCallback = () => Promise<any>;

const cbs: {[channel: string]: PubSubRequestCallback} = {}

export const usePubSubRequest = (channel: string,cb: PubSubRequestCallback) => {
    useEffect(() => {
        cbs[channel] = cb;
        return () => {
            if (cbs[channel]===cb) delete cbs[channel];
        };
    },[]);
};

export const pubSubRequest = async <T = any>(channel: string) => {
    if (cbs[channel]) {
        return (await cbs[channel]()) as T;
    }
    else {
        return null;
    }
};