import { useCssStateManager } from "../../../hooks/use_css_state_manager";
import { consumeReviewsHeader } from "../../../hooks/use_reviews_header";
import { SmartState } from "../../../hooks/use_smart_state";
import { misc } from "../../../lib/misc";
import { prebuildData } from "../../../lib/prebuild-data";
import { ConfigProduct } from "../../../scripts/pre-build/types";
import StarRating from "../../common/star_rating";
import PriceUpdate from "./price-update";

export type ProductHeaderProps = {
    product: ConfigProduct;
    currentPrice: SmartState<number>;
};

export default function ProductHeader({product,currentPrice}:ProductHeaderProps)
{
    const cssStateManager = useCssStateManager(["container"]);
    const discountedPrice = prebuildData.getProductPageDiscountedPrice(product.id,currentPrice.value).price;
    const { dollar,cents } = misc.splitPriceForDisplay(discountedPrice);

    let { starRating,reviewsCount } = consumeReviewsHeader(product.id);
    starRating = Math.min(Math.max(Math.round(starRating*2)/2,0),5);
    cssStateManager.useProperty(reviewsCount===0,"no-reviews");

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <div className="review-stars">       
                    <StarRating rating={starRating} scale={1.5} />
                </div>
                <div className="title"> 
                    {product.caption}
                </div>      
                <div className="price">
                    <span className="sup">$</span>{dollar}<span className="sup cents">{cents}</span>
                    <div className="price-update"><PriceUpdate price={discountedPrice} /></div>
                </div>
            </div>
            <style jsx>{`
            .container {
            }                        

            .no-reviews .review-stars {
                display: none;
            }

            .title {
                font-size: 24rem;
                margin-top: 6rem;
                letter-spacing: 1rem;            
            }
            
            .price {
                margin-top: 6rem;
                font-size: 27rem;
                font-weight: 600;                
            }

            .sup {
                font-size: 14rem;
                position: relative;
                top: -9rem;            
            }

            .sup.cents {
                left:1.5rem;
            }

            .price-update {
                display: inline-block;
                position: relative;
                top: -5rem;
                margin-left: 12rem;
            }
            `}</style>
        </>
    );
}