import { useContext, useEffect, useRef } from "react";
import { LayoutStateContext } from "../../../../contexts/layout_state";
import { useCssStateManager } from "../../../../hooks/use_css_state_manager";
import { SmartState, useSmartState } from "../../../../hooks/use_smart_state";
import { prebuildData } from "../../../../lib/prebuild-data";
import { OptionValueVariantSelect } from "../../../../lib/services/cart/types";
import OptionTitle from "./commons/title";

export type SelectWithQuantityProps = {
    title: string;
    subTitle?: string;
    unselectedCaption: string;
    entries: SelectWithQuantityEntry[];
    onValue?: (value: OptionValueVariantSelect | null) => void;
    loadFromSavedValue?: OptionValueVariantSelect;
    price?: number;
};

export type SelectWithQuantityEntry = {
    caption: string;
    value: string;
};

export type QuantityProps = {
    excludeZero?: boolean;
    value: SmartState<QuantityValue>;
    bypassCustomFocus?: boolean;
    onBypassCustomFocusDone?: () => void;
    inBadge?: boolean;
};

export type QuantityValue = {    
    value: number;
    valueDisplay: string;
    custom: boolean;
};

export function QuantitySelect({
        value,
        excludeZero = false,
        bypassCustomFocus = false,
        onBypassCustomFocusDone = () => {},
        inBadge = false
    }:QuantityProps) 
{
    const { mobileDetect } = useContext(LayoutStateContext);
    const { smallDesktopWidthCutoff: mobileCutoff } = mobileDetect;

    const cssStateManager = useCssStateManager(["container"]);
    cssStateManager.useProperty(value.value.custom,"editing");
    cssStateManager.useProperty(inBadge,"in-badge");

    const changeHandler = (index: number) => {
        value.value = {
            custom: (index===11),
            value: (index<11)?index:11,
            valueDisplay: (index<11)?"":"11"
        };
    };

    const textRef = useRef<HTMLInputElement>();

    useEffect(() => {
        if (value.value.custom) {
            if (textRef.current) {
                if (bypassCustomFocus) {
                    return onBypassCustomFocusDone();
                }
                textRef.current.focus();
                const val = textRef.current.value;
                textRef.current.value = "";
                textRef.current.value = val;
            }
        }
    },[value.value.custom]);

    const textChangeHandler = (textValue: string) => {
        textValue = textValue.split("").filter((ch) => /[0-9]/.test(ch)).join("");
        value.value = {
            custom: true,
            value: parseInt(textValue),
            valueDisplay: textValue
        };
    };

    const textBlurHandler = () => {
        let { valueDisplay: textValue } = value.value;
        const intValue = parseInt(textValue) || 0;
        if (intValue<11) {
            value.value = {
                custom: false,
                value: intValue,
                valueDisplay: ""
            };
        }
        else {
            value.value = {
                custom: true,
                value: intValue,
                valueDisplay: intValue.toString()
            };
        }
    };

    const { productOptions: { buttonSelectedTextColor,buttonBackgroundColor,buttonUnselectedTextColor } } = prebuildData.getActiveTheme();

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <select className="select" onChange={(e) => changeHandler(parseInt(e.target.value))} value={value.value.custom?"11":value.value.value.toString()}>
                    {Array(12).fill(0).map((_,index) =>                         
                        (!excludeZero || (index>0))?<option key={index} value={index.toString()}>{(index<11)?index.toString():"10+"}</option>:""
                    )}
                </select>
                <input ref={textRef} className="text" value={value.value.valueDisplay} onBlur={textBlurHandler} onChange={(e) => textChangeHandler(e.target.value)} />
                <div className="title">
                    Quantity
                    <div className="cover"></div>
                </div>
            </div>
            <style jsx>{`
                .container {
                    width: 100%;
                    position: relative;
                }
                .text {
                    width: 100%;
                    height: 45rem;
                    outline: none;
                    border: 1rem solid rgba(190, 190, 190);
                    border-radius: 5rem;
                    font-size: 16rem;
                    font-family: "Open Sans", "Arial", "Helvetica", sans-serif;
                    padding-left: 13rem;
                    color: ${buttonSelectedTextColor};
                    background-color: ${buttonBackgroundColor};
                    font-weight: 600;
                    display: none;
                }
                .select {
                    cursor: pointer;
                    height: 45rem;
                    outline: none;
                    border: 1rem solid rgba(190, 190, 190);
                    border-radius: 5rem;
                    width: 100%;
                    font-size: 16rem;
                    font-family: "Open Sans", "Arial", "Helvetica", sans-serif;
                    padding: 0rem 9rem;
                    color: ${buttonSelectedTextColor};
                    background-color: ${buttonBackgroundColor};
                    font-weight: 600;
                }
                .title {
                    position: absolute;
                    left: calc(50% - 5rem);
                    top: -8rem;
                    transform: translateX(-50%);
                    font-size: 10rem;
                    color: ${buttonUnselectedTextColor};                    
                    background-color: var(--body-background-color);
                    padding: 0rem 2rem;
                }
                .title .cover {
                    display: none;
                }
                .editing .text {
                    display: block;
                }
                .editing .select {
                    display: none;
                }

                .select,.text {
                    background-color: ${buttonBackgroundColor};
                }

                .title {
                    background-color: transparent;
                }

                .title .cover {
                    display: block;
                    position: absolute;
                    left: 0rem;
                    right: 0rem;
                    top: 8rem;
                    bottom: 0rem;
                    background-color: ${buttonBackgroundColor};
                    z-index: -1;
                }

                @media screen and (max-width: ${mobileCutoff}px) {
                }

            `}</style>
        </>
    );
}

export default function SelectWithQuantity({
        title,
        subTitle = "",
        unselectedCaption,
        entries,
        onValue = () => {},
        loadFromSavedValue,
        price
    }:SelectWithQuantityProps)
{
    const { mobileDetect } = useContext(LayoutStateContext);
    const { smallDesktopWidthCutoff: mobileCutoff } = mobileDetect;

    const unselectedValue = "--unselected--";
    const removeCaption = "-- Remove --";
    const removeValue = "--remove--";
    const cssStateManager = useCssStateManager(["container"]);
    const selectValue = useSmartState("--unselected--");
    cssStateManager.useProperty(selectValue.value!==unselectedValue,"selected");
    const initialQuantityValue = {custom: false,value: 1,valueDisplay: ""};
    const quantityValue = useSmartState<QuantityValue>(initialQuantityValue,{
        onSet: (value) => {
            if ((!value.custom) && (value.value===0)) {
                selectValue.value = unselectedValue;
                quantityValue.value = {
                    ...initialQuantityValue
                };
            }
            else {
                if (selectValue.value===unselectedValue) {
                    onValue(null);
                }
                else {
                    onValue({
                        type: "variant-select",
                        id: selectValue.value,
                        quantity: value.value
                    });
                }
            }                        
        }
    });

    

    const bypassCustomFocusRef = useRef(false);
    const bypassCustomFocusDoneHandler = () => {
        bypassCustomFocusRef.current = false;
    };

    useEffect(() => {
        if (loadFromSavedValue) {
            bypassCustomFocusRef.current = true;
            selectValue.value = loadFromSavedValue.id;
            quantityValue.value = {
                custom: (loadFromSavedValue.quantity!>10),
                value: loadFromSavedValue.quantity!,
                valueDisplay: loadFromSavedValue.quantity!.toString()
            };
        }
    },[loadFromSavedValue]);

    useEffect(() => {
        if (selectValue.value===removeValue) {
            selectValue.value = unselectedValue;
            quantityValue.value = {
                ...initialQuantityValue
            };
        }
    },[selectValue.value]);

    const { productOptions: { buttonSelectedTextColor,buttonBackgroundColor,buttonUnselectedTextColor } } = prebuildData.getActiveTheme();

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <OptionTitle caption={title} subCaption={subTitle} price={price?price*quantityValue.value.value:undefined} priceEnabled={selectValue.value!==unselectedValue} />
                <div className="controls">
                    <select className="select" onChange={(e) => {
                        selectValue.value = e.target.value;
                        if (quantityValue.value.value!==1) {
                            quantityValue.value = {...initialQuantityValue};
                        }
                        else {
                            onValue({
                                type: "variant-select",
                                id: e.target.value,
                                quantity: quantityValue.value.value
                            });    
                        }
                    }} value={selectValue.value}>
                        <option className="hidden-option" value={unselectedValue}>{unselectedCaption}</option>
                        <option className="remove-option" value={removeValue}>{removeCaption}</option>                        
                        {entries.map(({caption,value},index) => <option key={index} value={value}>{caption}</option>)}
                    </select>
                    <div className="quantity-wrapper">
                        <QuantitySelect 
                            value={quantityValue} 
                            bypassCustomFocus={bypassCustomFocusRef.current}
                            onBypassCustomFocusDone={bypassCustomFocusDoneHandler}
                        />
                    </div>
                </div>
            </div>
            <style jsx>{`
            .container {
                padding-bottom: 16rem;
            }
            .controls {
                display: flex;
            }
            .select {
                cursor: pointer;
                height: 45rem;
                outline: none;
                border: 1rem solid rgba(190, 190, 190);
                border-radius: 5rem;
                width: 100%;
                font-size: 16rem;
                font-family: "Open Sans", "Arial", "Helvetica", sans-serif;
                padding: 0rem 9rem;
                color: ${buttonUnselectedTextColor};
                background-color: ${buttonBackgroundColor};
                font-weight: 600;            
                flex: 1 1;
            }
            .select option {
                color: ${buttonSelectedTextColor};
            }
            .selected .select {
                color: ${buttonSelectedTextColor};
            }

            .remove-option {
                display: none;   
            }

            .selected .remove-option {
                display: block;
            }

            .hidden-option {
                display: none;
            }
            .quantity-wrapper {
                flex: 0 0 80rem;
                padding-left: 8rem;
                display: none;
            }        
            .selected .quantity-wrapper {
                display: block;
            }   

            @media screen and (max-width: ${mobileCutoff}px) {
                .select {
                    background-color: var(--body-background-color);
                }
            }
            `}</style>
        </>
    );
}