import { useContext, useRef } from "react";
import { LayoutStateContext } from "../../../contexts/layout_state";
import { useCachedValue } from "../../../hooks/use_cached_value";
import { useCssStateManager } from "../../../hooks/use_css_state_manager";
import { useCustomRouter } from "../../../hooks/use_custom_router";
import { useDesktopScrollbar } from "../../../hooks/use_desktop_scrollbar";
import { prebuildData } from "../../../lib/prebuild-data";
import VerticalSlider from "../../common/vertical-slider";
import GridItem, { GridItemData } from "../../side_cart/also_bought/grid_item";

export type FrequentlyBoughtWithProps = {
    productIds: string[];
};

type ProductItemProps = { id: string };

function ProductItem({id}: ProductItemProps) {
    const router = useCustomRouter();

    const dataRef = useRef<GridItemData>();
    if (!dataRef.current) {
        const product = prebuildData.getProductById(id);
        if (product) {
            const {caption,smallImages,id} = product;
            dataRef.current = {
                imageSrc: prebuildData.bustCache(smallImages[0].src),
                imageHoverSrc: (smallImages.length>1)?prebuildData.bustCache(smallImages[1].src):"",
                title: caption,
                productId: id.toString()
            };
        }
    }

    const productPageRoute = useCachedValue(() => prebuildData.getProductPageRoute(id),id);

    const clickHandler = () => {
        router.push(productPageRoute);
    };

    if (dataRef.current) {
        return <GridItem data={dataRef.current} clickHandler={clickHandler} />;
    }
    else {
        return null;
    }
}

export default function FrequentlyBoughtWith({productIds}:FrequentlyBoughtWithProps)
{
    const { mobileDetect } = useContext(LayoutStateContext);
    const { smallDesktopWidthCutoff: mobileCutoff,isSmallDesktopScreen: isMobile } = mobileDetect;

    let { ref: sliderRef,styles: sliderStyles,className: sliderClassName,injectedStyles: sliderInjectedStyles } = useDesktopScrollbar();

    const cssStateManager = useCssStateManager(["frequently-bought-with"]);

    const { alsoBoughtWith: { titleColor } } = prebuildData.getActiveTheme();

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <div className="title">Frequently Bought With</div>
                {isMobile.current?(
                    <div ref={sliderRef} className={["mobile-slider",sliderClassName].join(" ")} style={sliderStyles}>
                        <div className="inner">
                            {productIds.map((id,index) => <div key={id} className={`product-item ${(index===0)?"first":""}`}><ProductItem id={id} /></div>)}
                        </div>
                    </div>
                ):(
                    <div className="vertical-slider">
                        <VerticalSlider>
                            {productIds.map((id) => <ProductItem key={id} id={id} />)}
                        </VerticalSlider>
                    </div>
                )}
            </div>
            <style jsx>{sliderInjectedStyles}</style>
            <style jsx>{`
            .frequently-bought-with {
                padding-top: 20rem;
                margin-bottom: -20rem;
            }
            .title {
                text-align: center;
                font-size: 17rem;
                font-weight: 600;
                color: ${titleColor};
                padding-bottom: 40rem;
            }
            .grid-item {
                width: 150rem;
            }

            @media screen and (max-width: ${mobileCutoff}px) {
                .frequently-bought-with {
                    width: 100%;
                }
                .mobile-slider {
                    overflow-x: auto;
                    width: 100%;
                    padding-bottom: 20rem;
                    margin-bottom: -20rem;
                }

                .mobile-slider .inner {
                    display: flex;
                    flex-direction: row;
                }

                .product-item {
                    flex: 0 0 150rem;
                    margin-left: 20rem;
                    margin-bottom: 10rem;
                }
                .product-item.first {
                    margin-left: 0rem;
                }
            }
            `}</style>
        </>
    );
}