import { useContext, useEffect, useRef } from "react";
import { LayoutStateContext } from "../../../../contexts/layout_state";
import { useCssStateManager } from "../../../../hooks/use_css_state_manager";
import { prebuildData } from "../../../../lib/prebuild-data";
import { OptionValueCheckbox } from "../../../../lib/services/cart/types";
import { EventPane } from "../../../core/event_pane";
import SquareImage from "../../../core/square_image";
import OptionTitle from "./commons/title";

export type CheckboxOptionProps = {
    title: string;
    subTitle?: string;
    productId: string;
    description: string;
    titleStyle?: "external" | "internal";
    included?: boolean;
    onValue?: (value: OptionValueCheckbox) => void;
    loadFromSavedValue?: OptionValueCheckbox;
    inBadge?: boolean;
    price?: number;
};

export default function CheckboxOption({
        title,
        productId,
        description,
        subTitle = "",
        titleStyle = "external",
        included = false,
        onValue = () => {},
        loadFromSavedValue,
        inBadge = false,
        price
    }:CheckboxOptionProps)
{
    const { mobileDetect } = useContext(LayoutStateContext);
    const { smallDesktopWidthCutoff: mobileCutoff } = mobileDetect;

    const padding = 1;
    const cssStateManager = useCssStateManager(["container"]);
    const selected = cssStateManager.useState(false,"selected");
    cssStateManager.useProperty(titleStyle==="internal","internal");
    cssStateManager.useProperty(inBadge,"in-badge");    
    cssStateManager.useProperty(included,"included");    
    const thumbSrcRef = useRef("");
    if (thumbSrcRef.current==="") {
        const product = prebuildData.getProductById(productId);
        if (product) {
            thumbSrcRef.current = prebuildData.bustCache(product.smallImages[0].src);            
        }
    }

    useEffect(() => {
        if (loadFromSavedValue) {
            handleClick();
        }
    },[loadFromSavedValue]);

    const handleClick = () => {
        const newValue = !selected.value;
        selected.value = newValue;
        if (newValue) {
            onValue({
                type: "checkbox",                
            });
        }
        else {
            onValue(null);
        }
    };

    const { productOptions: { buttonSelectedBackgroundColor,buttonBackgroundColor,addonsDescriptionColor,addonsIncludedDescriptionColor } } = prebuildData.getActiveTheme();

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                {(titleStyle==="external")?<OptionTitle caption={title} subCaption={subTitle} price={price} priceEnabled={selected.value} />:""}
                <div className="box">
                    <div className="outline"></div>
                    <div className="image">
                        {thumbSrcRef.current?<SquareImage src={thumbSrcRef.current} fadeIn />:""}                        
                    </div>                    
                    <div className="description-frame">
                        <div className="internal-title">{title}</div>
                        <div className="description">
                            {description}
                        </div>
                    </div>
                    {!included?<EventPane onClick={handleClick} />:""}                    
                </div>
            </div>
            <style jsx>{`
            .container {
                --selected-color: ${buttonSelectedBackgroundColor};
            }
            .box {
                position: relative;
                width: 100%;
                border: 1rem solid rgba(190,190,190);
                border-radius: 5rem;
                padding: 10rem;
                display: flex;                
                /* align-items: center; */
                background-color: ${buttonBackgroundColor};
                transition: background-color 0.3s;
            }
            .selected .box,.included .box {
                background-color: var(--selected-color);
            }
            .outline {
                position: absolute;
                left: -${padding}rem;
                top: -${padding}rem;
                bottom: -${padding}rem;
                right: -${padding}rem;
                border: 2rem solid #9ba2fb;
                border-radius: 5rem;
                opacity: 0;
                transition: opacity 0.3s;                
            }
            .selected .outline {
                opacity: 1;
            }
            .image {
                flex: 0 0 60rem;
            }
            .description-frame {
                display: flex;
                flex-direction: column;
                justify-content: center;
                padding-left: 10rem;
            }
            .internal .description-frame {
                justify-content: start;
            }
            .description {
                font-size: 12rem;
                color: ${addonsDescriptionColor};
            }
            .internal-title {
                display: none;
                font-size: 12rem;
                font-weight: 600;
                color: ${addonsIncludedDescriptionColor};
                margin-bottom: 5rem;
                padding-top: 3rem;
            }
            .internal .internal-title {
                display: block;
            }
            .in-badge .box {
                border: none;
                background-color: transparent;
            }
            .in-badge {
                text-align: left;
            }
            .in-badge .image {
                flex: 0 0 54rem;
                position: relative;
                top: 1rem;
            }
            .in-badge .description {
                font-size: 10rem;
            }
            .in-badge .internal-title {
                font-size: 11rem;
            }
            @media screen and (max-width: ${mobileCutoff}px) {
                .box {
                    --selected-color: ${buttonBackgroundColor};
                    background-color: var(--body-background-color);
                }

                .outline {
                }
            }
            `}</style>
        </>
    );
}