import { useContext } from "react";
import { LayoutStateContext } from "../../../../../contexts/layout_state";
import { useCssStateManager } from "../../../../../hooks/use_css_state_manager";
import { SmartState } from "../../../../../hooks/use_smart_state";
import { OptionsOptionButtonsQuantity } from "../../../../../lib/shared/options/types";
import { prebuildData } from "../../../../../lib/prebuild-data";
import PriceDisplay from "../../../../common/price-display";
import { EventPane } from "../../../../core/event_pane";

export type OptionTitleProps = {
    caption: string;
    subCaption?: string;
    quantity?: OptionsOptionButtonsQuantity;
    quantityOn?: SmartState<boolean>;
    price?: number;
    priceEnabled?: boolean;
};

export type QuantityOptionProps = {
    quantity: OptionsOptionButtonsQuantity;
    quantityOn: SmartState<boolean>;
};

export type YesNoProps = {
    quantityOn: SmartState<boolean>;
};

function YesNo({quantityOn}:YesNoProps)
{
    const cssStateManager = useCssStateManager(["yes-no"]);
    cssStateManager.useProperty(quantityOn.value,"on");    

    const clickHandler = () => {
        quantityOn.value = !quantityOn.value;
    };

    const { productOptions: { sliderNoColor,sliderNoTextColor,sliderYesColor,sliderYesTextColor } } = prebuildData.getActiveTheme();

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <div className="switch">
                    <div className="slider"></div>
                    <div className="option right">No</div>
                    <div className="option left">Yes</div>
                </div>
                <EventPane onClick={clickHandler} />
            </div>
            <style jsx>{`
            .yes-no {
                display: inline-block;
                position: relative;
                left: 6rem;           
                top: 5rem; 
                font-size: 15rem;
                --height: 22rem;
                --width: 48rem;
                --padding: 3rem;
                --slider-size: calc(var(--height) - (var(--padding) * 2));
                --duration: 0.2s;
            }
            .switch {
                position: relative;
                width: var(--width);
                height: var(--height);
                background-color: ${sliderNoColor};
                border-radius: var(--height);
                transition: background-color var(--duration);
            }
            .on .switch {
                background-color: ${sliderYesColor};
            }
            .slider {
                position: absolute;                                
                height: var(--slider-size);
                width: var(--slider-size);
                background-color: ${sliderNoTextColor};
                top: var(--padding);
                left: var(--padding);
                border-radius: var(--slider-size);
                transition: transform var(--duration);
            }
            .on .slider {
                transform: translateX(calc(var(--width) - (var(--padding) * 2) - var(--slider-size)));
                background-color: ${sliderYesTextColor};
            }
            .option {
                position: absolute;                
                top: 50%;
                transform: translateY(-8rem);
                font-size: 11rem;
                font-weight: 600;
                transition: opacity var(--duration);
            }
            .option.left {
                opacity: 0;
                left: calc(var(--padding) + 5rem);
                color: ${sliderYesTextColor};
            }
            .option.right {
                opacity: 1;
                right: calc(var(--padding) + 6rem);
                color: ${sliderNoTextColor};
            }

            .on .option.left {
                opacity: 1;
            }
            .on .option.right {
                opacity: 0;
            }
            `}</style>
        </>
    );
}

function QuantityOption({quantity,quantityOn}:QuantityOptionProps)
{    
    const { mobileDetect } = useContext(LayoutStateContext);
    const { smallDesktopWidthCutoff: mobileCutoff } = mobileDetect;

    const { productOptions: { lightTextColor } } = prebuildData.getActiveTheme();

    const cssStateManager = useCssStateManager(["quantity-option"]);
    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                {quantity.caption} <YesNo quantityOn={quantityOn} />
            </div>
            <style jsx>{`
            .quantity-option {
                display: inline-block;
                color: ${lightTextColor};
                font-size: 17rem;
                position: relative;
                top: -2rem;                
                left: 10rem;            
            }

            @media screen and (max-width: ${mobileCutoff}px) {
                .quantity-option {
                    left: 0rem;
                    font-size: 16rem;
                }
            }
            `}</style>
        </>
    );
}

export default function OptionTitle({caption,subCaption,quantity,quantityOn,price,priceEnabled = false}:OptionTitleProps)
{
    const { mobileDetect } = useContext(LayoutStateContext);
    const { smallDesktopWidthCutoff: mobileCutoff } = mobileDetect;

    const cssStateManager = useCssStateManager(["option-title"]);
    cssStateManager.useProperty(priceEnabled,"price-enabled");

    const { productOptions: {
        textColor,
        lightTextColor,
        addonsPriceSelectedTextColor,
        addonsPriceUnselectedTextColor
    } } = prebuildData.getActiveTheme();

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                {caption} {subCaption?<span>{subCaption}</span>:""} <br className="br" />{(quantity && quantityOn)?<QuantityOption quantity={quantity} quantityOn={quantityOn} />:""}
                {(typeof price==="number")?(
                    <div className="price">
                        <PriceDisplay price={price} size={18} plus />
                    </div>
                ):""}
            </div>
            <style jsx>{`
            .option-title {
                font-size: 23rem;
                font-weight: 600;
                color: ${textColor};
                margin-bottom: 20rem;
                position: relative;
            }
            .option-title span {
                color: ${lightTextColor};
                font-size: 16rem;
                position: relative;
                top: -2rem;                
                left: 3rem;            
            }
            .price {
                position: absolute;
                color: ${addonsPriceUnselectedTextColor};
                top: -5rem;
                right: 2rem;
                transition: color 0.3s;
            }
            .price-enabled .price {
                color: ${addonsPriceSelectedTextColor};
            }
            .br {
                display: none;
            }

            @media screen and (max-width: ${mobileCutoff}px) {
                .br {
                    display: inline;
                }
            }
            `}</style>
        </>
    );
}