import { useContext, useEffect, useRef } from "react";
import { LayoutStateContext } from "../../../../contexts/layout_state";
import { useCssStateManager } from "../../../../hooks/use_css_state_manager";
import { useSmartState } from "../../../../hooks/use_smart_state";
import { OptionValueButton, OptionValueCheckboxGroup } from "../../../../lib/services/cart/types";
import { Button } from "./buttons";
import OptionTitle from "./commons/title";

export type CheckboxGroupOptionProps = {
    title: string;
    subTitle?: string;
    entries: CheckboxGroupEntry[];
    onValue?: (value: OptionValueCheckboxGroup | null) => void;
    loadFromSavedValue?: OptionValueCheckboxGroup;
};

export type CheckboxGroupEntry = {
    caption: string;
    value: string;
    subCaption?: string;    
};

export default function CheckboxGroupOption({
        title,
        entries,
        subTitle = "",
        onValue = () => {},
        loadFromSavedValue
    }:CheckboxGroupOptionProps)
{
    const cssStateManager = useCssStateManager(["container"]);

    const selected = useSmartState(Array(entries.length).fill(false));
    const toggleSelected = (index: number) => {
        const newValue = [...selected.value];
        newValue[index] = !newValue[index];
        selected.value = newValue;
        const ids = entries.map(({value},index) => value).filter((_,index) => newValue[index]);
        onValue((ids.length>0)?{
            type: "checkbox-group",
            ids
        }:null);
    };

    const loadFromSavedValueRef = useRef<{[id: string]: OptionValueButton}>({});    

    return (
        <>
            <div className={cssStateManager.getClassNames()}>                
                <OptionTitle caption={title} subCaption={subTitle} />
                <div className="buttons">
                    {entries.map(({caption,subCaption,value},index) => 
                        <Button 
                            id={value}
                            key={index} 
                            text={caption} 
                            subText={subCaption}
                            selected={selected.value[index]}
                            onSelected={() => toggleSelected(index)}
                            canToggle={true}                            
                            loadFromSavedValue={(loadFromSavedValueRef.current[value] = loadFromSavedValueRef.current[value] || (loadFromSavedValue?((loadFromSavedValue.ids.indexOf(value)!==-1)?{type: "buttons",id: value}:undefined):undefined))}
                        />
                    )}
                </div>
            </div>
            <style jsx>{`
            .container {
            }
            .buttons {}
            `}</style>
        </>
    );
}