import { useContext, useEffect, useRef, useState } from "react";
import { useCssStateManager } from "../../../../hooks/use_css_state_manager";
import { useSmartState } from "../../../../hooks/use_smart_state";
import { EventPane } from "../../../core/event_pane";
import OptionTitle from "./commons/title";
import { misc } from "../../../../lib/misc";
import { prebuildData } from "../../../../lib/prebuild-data";
import { OptionValueButton } from "../../../../lib/services/cart/types";
import { OptionsOptionButtonsQuantity } from "../../../../lib/shared/options/types";
import { QuantitySelect, QuantityValue } from "./select-with-quantity";
import { LayoutStateContext } from "../../../../contexts/layout_state";

export type ButtonEntry = {
    caption: string;
    id: string;
    custom?: boolean;
    customRange?: ButtonCustomRange; 
    customDisplay?: string; 
    customPlaceholder?: string;
    slideshowMatch?: string;
};

export type ButtonsOptionProps = {
    title: string;
    subTitle?: string;
    buttons: ButtonEntry[];
    onSlideShowMatch?: (value: string,bypassSlideshowAnimation?: boolean) => void;
    minWidth?: number;
    onValue?: (value: OptionValueButton,auto?: boolean) => void;
    loadFromSavedValue?: OptionValueButton;
    quantity?: OptionsOptionButtonsQuantity;
};

export type ButtonCustomRange = {
    min: number;
    max: number;
    decimalUnit: number;
};

type ButtonProps = {
    id: string;
    text: string;
    subText?: string;
    minWidth?: number;
    selected: boolean;
    unselected?: boolean;
    onSelected?: (auto?: boolean) => void;
    custom?: boolean;
    customRange?: ButtonCustomRange;
    customDisplay?: string;
    loadFromSavedValue?: OptionValueButton;
    canToggle?: boolean;
    customPlaceholder?: string;
    onCustomValue?: (value: number,auto?: boolean) => void;
};

export function Button({
        id,
        text,
        minWidth,
        selected,
        unselected,
        onSelected = () => {},
        custom = false,
        loadFromSavedValue,
        canToggle = false,
        customRange = {min: 0,max: 50,decimalUnit: 5},
        subText = "",
        customDisplay = "({value}) => value;",
        customPlaceholder,
        onCustomValue = () => {}
    }: ButtonProps)
{
    const { mobileDetect } = useContext(LayoutStateContext);
    const customSubText = `${customRange.min} - ${customRange.max}`;
    const customSubCaptionExtra = (customRange.decimalUnit>0)?`0.${customRange.decimalUnit}`:"";
    const hasDecimal = (customRange.decimalUnit>0);
    const padding = 1;

    const cssStateManager = useCssStateManager(["container"]);
    cssStateManager.useProperty(selected,"selected");
    cssStateManager.useProperty(unselected,"unselected");
    cssStateManager.useProperty(custom,"custom");
    cssStateManager.useProperty(!!customSubText,"with-custom-subtext");
    cssStateManager.useProperty(hasDecimal,"has-decimal");
    cssStateManager.useProperty(!!subText,"with-sub-text");
    const editing = cssStateManager.useState(true,"editing");
    const showCustomValue = useSmartState(false);
    const disableTransition = cssStateManager.useState(true,"disable-transition");
    const customValue = useSmartState("");

    const customIntLength = Math.floor(customRange.max).toString().length;
    const customPlaceholderText = customPlaceholder || (hasDecimal?"0".repeat(customIntLength)+".0":"");

    const inputRefDesktop = useRef<HTMLInputElement>();
    const inputRefMobile = useRef<HTMLInputElement>();

    const { isSmallDesktopScreen: isMobileActive,smallDesktopWidthCutoff: mobileCutoff } = mobileDetect;
    const inputRefFn = () => isMobileActive.current?inputRefMobile:inputRefDesktop;

    const customDisplayFnRef = useRef<any>();    

    const bypassSelectionRef = useRef(false);

    useEffect(() => {
        if (loadFromSavedValue) {
            if (loadFromSavedValue.id===id) {
                if ((custom) && (loadFromSavedValue.hasOwnProperty("value"))) {
                    bypassSelectionRef.current = true;
                    onSelected(true);
                    updateValue(loadFromSavedValue.value.toString(),true);
                }
                else {
                    onSelected(true);
                }                
            }
        }
    },[loadFromSavedValue]);    

    useEffect(() => {
        if (selected) {
            if (custom) {
                if (bypassSelectionRef.current) {
                    bypassSelectionRef.current = false;
                    return;
                }
                editing.value = true;
                const inputRef = inputRefFn();
                setTimeout(() => {
                    inputRef.current?.focus();    
                    const val = inputRef.current.value;
                    inputRef.current.value = "";
                    inputRef.current.value = val;
                },50);
                // inputRef.current?.focus();
            }                                    
        }
        else {
            if (custom) {
                showCustomValue.value = false;
            }
        }
    },[selected]);

    const inputValue = useSmartState("");
    const inputLeftShift = useSmartState(0);

    const inputOffsets = {
        beforeDot: 0,
        // afterDot: -4.664,
        afterDot: -4.4,
        afterAll: -13.297
    };

    const updateValue = (newValue: string,auto?: boolean) => {
        disableTransition.value = true;
        customValue.value = newValue;
        inputValue.value = newValue;
        editing.value = false;
        showCustomValue.value = true;
        onCustomValue(parseFloat(newValue),auto);
    };

    const inputSet = () => {
        if ((!custom) || (!editing.value)) return;
        let { value } = inputValue;
        if (value.endsWith(".")) value+= "0";
        let value10 = Math.round((parseFloat(value) || 0)*10);
        const min10 = customRange.min*10;
        const max10 = customRange.max*10;
        value10 = Math.max(min10,Math.min(max10,value10));
        let decimal = value10%10;
        const int = Math.round((value10-decimal)/10);
        if (hasDecimal) {
            decimal = Math.floor(decimal/customRange.decimalUnit)*customRange.decimalUnit;
        }
        const newValue = (decimal>0)?`${int}.${decimal}`:int.toString();
        updateValue(newValue);
    };

    const inputChangeHandler = (value: string) => {        
        let dotFound = false;
        value = value.split("").filter((ch) => {
            if (ch===".") {
                if (!hasDecimal) return false;
                if (dotFound) return false;
                dotFound = true;
                return true;
            }
            else {
                return /[0-9]/.test(ch);
            }
        }).join("");
        const maxLength = customIntLength+(hasDecimal?1:0);
        const maxLengthWithoutDecimal = customIntLength;
        const processDotLess = (value: string) => {
            value = value.substring(0,maxLength);
            if (value.length<=maxLengthWithoutDecimal) {
                inputValue.value = value;
            }
            else {
                value = value.substring(0,maxLength-1)+"."+value.substring(maxLength-1);
                inputValue.value = value;
            }
        };
        if (dotFound) {
            if (value.charAt(0)===".") value = "0"+value;
            let [before,after] = value.split(".");
            if (before.length>maxLengthWithoutDecimal) {
                processDotLess(before);
            }
            else {
                after = after.substring(0,1);
                value = before+"."+after;
                inputValue.value = value;
            }
        }
        else {
            processDotLess(value);
        }
    };

    useEffect(() => {
        if (disableTransition.value) {
            setTimeout(() => {
                disableTransition.value = false;
            },300);
        }
    },[disableTransition.value]);

    const clickHandler = () => {
        if (canToggle) return onSelected();
        if (selected) {
            if ((custom) && (!editing.value)) {
                disableTransition.value = true;
                editing.value = true;
                const inputRef = inputRefFn();
                if (inputRef.current) {
                    inputRef.current.focus();
                    const val = inputRef.current.value;
                    inputRef.current.value = "";
                    inputRef.current.value = val;
                }
            }            
        }
        else {
            onSelected();
        }     
    };
    const handleInputKeyUp = (code: string) => {
        if ((code==="Enter") || (code==="Escape")) {
            inputSet();
        }        
    };

    const customValueDisplay = (value: string) => {
        const fn = (customDisplayFnRef.current = customDisplayFnRef.current || prebuildData.evalInContext(customDisplay));        
        return fn({value});
    };
    
    const { productOptions: {
        buttonSelectedBackgroundColor,
        buttonBorderColor,
        buttonSelectedTextColor,
        buttonBackgroundColor,
        buttonUnselectedTextColor,
        sliderNoColor,
        lightTextColor,
        buttonSelectedBorderColor
    } } = prebuildData.getActiveTheme();

    return <>
        <div className={cssStateManager.getClassNames()} style={{...(((typeof minWidth==="number") && (minWidth>0))?{minWidth: `${minWidth}rem`}:{})}}>
            <div className="decimal-placeholder centered" style={{display: (inputValue.value!=="")?"none":"block"}}>{customPlaceholderText}</div>            
            <div className="center-text">
                {(customValue.value && selected)?((showCustomValue.value)?customValueDisplay(customValue.value):<div dangerouslySetInnerHTML={{ __html: "&nbsp;" }} />):text}
                <div className="sub-text">{subText}</div>
            </div>
            <div className="outline"></div>
            <div className="custom-title">
                <div className="cover"></div>
                <div>{text}</div>
            </div>
            <div className="custom-sub-title">
                <div>{customSubText}{customSubCaptionExtra?<span>&nbsp;({customSubCaptionExtra})</span>:""}</div>
            </div>
            <input 
                onKeyUp={(e) => handleInputKeyUp(e.code)} 
                onBlur={inputSet} 
                onChange={(e) => inputChangeHandler(e.target.value)} 
                value={inputValue.value} 
                ref={inputRefDesktop} 
                className="decimal-input decimal-input-desktop" 
                type="text"
            />
            <input 
                onKeyUp={(e) => handleInputKeyUp(e.code)} 
                onBlur={inputSet} 
                onChange={(e) => inputChangeHandler(e.target.value)} 
                value={inputValue.value} 
                ref={inputRefMobile}
                className="decimal-input decimal-input-mobile" 
                type="number"
            />
            <div className="event-pane">
                <EventPane onClick={clickHandler} />
            </div>
        </div>
        <style jsx>{`            
        .container {
            --selected-color: ${buttonSelectedBackgroundColor};
            position: relative;
            display: inline-block;
            padding: 12rem 0rem;
            border: 1rem solid ${buttonBorderColor};
            margin-right: 15rem;
            margin-bottom: 16rem;
            min-width: 95rem;
            text-align: center;
            border-radius: 5rem;
            color: ${buttonSelectedTextColor};
            font-weight: 600;
            background-color: ${buttonBackgroundColor};
            transition: background-color 0.3s;
        }
        .selected.custom.editing .event-pane {
            display: none;
        }
        .decimal-input {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            width: 100%;
            background-color: transparent;
            text-align: center;
            border: none;
            outline: none;
            font-size: 16rem;
            font-weight: 600;
            color: ${buttonSelectedTextColor};
            padding-top: 1rem;
            opacity: 0;
            /* transition: opacity 0.3s; */
            font-family: "Open Sans", "Arial", "Helvetica", sans-serif;
        }
        .decimal-input-desktop {
            display: block;
        }
        .decimal-input-mobile {
            display: none;
        }
        .disable-transition .decimal-input {
            transition-duration: 0s;
        }
        .selected.custom.editing .decimal-input {
            opacity: 1;
        }
        .decimal-placeholder {
            color: ${buttonBorderColor};
            opacity: 0;
            transition: opacity 0.3s;
        }
        .selected.custom.has-decimal.editing .decimal-placeholder {
            opacity: 1;
        }
        .show-on-custom-selected {
            opacity: 0;
            transition: opacity 0.3s;
        }
        .selected.custom .show-on-custom-selected {
            opacity: 1;
        }
        .centered {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
        }
        .center-text {
            opacity: 1;
            transition: opacity 0.3s;
            padding: 0rem 15rem;
        }
        
        .unselected .center-text {
            color: ${buttonUnselectedTextColor};
        }
        .disable-transition .center-text {
            transition-duration: 0s;
        }
        .selected.custom.editing .center-text {
            opacity: 0;
        }
        .custom-title,.custom-sub-title {
            position: absolute;
            font-size: 13rem;
            font-weight: 400;
            left: 50%;
            transform: translateX(-50%);
            opacity: 0;
            transition: opacity 0.3s;
            padding: 0rem 4rem;
            white-space: nowrap;
        }
        .custom-sub-title span {
            color: ${sliderNoColor};
        }
        .custom-title {
            top: -10rem;
        }
        .custom-title .cover {
            position: absolute;            
            left: 0rem;
            right: 0rem;
            height: 9rem;
            background-color: var(--selected-color);
            z-index: -1;
        }
        .custom-title .cover {
            bottom: 0rem;
        }
        .custom-sub-title {
            bottom: -18rem;
            font-size: 11rem;
        }
        .sub-text {
            color: ${lightTextColor};
            font-size: 11rem;
            font-weight: 400;
            padding-top: 2rem;
            display: none;
        }
        .with-sub-text .sub-text {
            display: block;
        }
        .selected.custom .custom-title,.selected.editing.custom.with-custom-subtext .custom-sub-title {
            opacity: 1;
        }
        .container.selected {
            background-color: var(--selected-color);
        }
        .container.hover {
            border: 1rem solid #888;
        }
        .outline {
            position: absolute;
            left: -${padding}rem;
            top: -${padding}rem;
            bottom: -${padding}rem;
            right: -${padding}rem;
            border: 2rem solid ${buttonSelectedBorderColor};
            border-radius: 5rem;
            opacity: 0;
            transition: opacity 0.3s;
        }
        .selected .outline {
            opacity: 1;
        }

        @media screen and (max-width: ${mobileCutoff}px) {
            .container {
                --selected-color: ${buttonBackgroundColor};
                background-color: var(--body-background-color);
            }
            .decimal-input-desktop {
                display: none;
            }
            .decimal-input-mobile {
                display: block;
            }
        }
        `}</style>
    </>;
}


export default function ButtonsOption({buttons,minWidth,onSlideShowMatch = () => {},title,subTitle = "",onValue = () => {},loadFromSavedValue,quantity}:ButtonsOptionProps)
{
    const cssStateManager = useCssStateManager(["container"]);
    const { scalePixelToRem } = useContext(LayoutStateContext);

    const selectedIndex = useSmartState(-1);
    const initialQuantityValue = {custom: false,value: 1,valueDisplay: ""};
    const quantityValueToLoadRef = useRef<QuantityValue>();
    const quantityOn = quantity?useSmartState(false,{onSet: (value) => {
        if (value) {
            quantityValue.value = quantityValueToLoadRef.current || {...initialQuantityValue};
            quantityValueToLoadRef.current = undefined;
        }
        else {
            if (selectedIndex.value!==-1) {
                onValue({
                    type: "buttons",
                    id: buttons[selectedIndex.value].id
                });
            }    
        }
    }}):undefined;    
    const quantityValue = quantity?useSmartState<QuantityValue>(initialQuantityValue,{onSet: (value) => {
        if (selectedIndex.value!==-1) {
            onValue({
                type: "buttons",
                id: buttons[selectedIndex.value].id,
                quantity: value.value
            });
        }
    }}):undefined;
    const quantityWrapperRef = quantity?useRef<HTMLDivElement>():undefined;
    if (quantityOn) {
        cssStateManager.useProperty(quantityOn.value,"quantity-on");        
    }

    const quantityWrapperHeight = Math.ceil(scalePixelToRem(quantityWrapperRef?.current?.offsetHeight)) || 0;
    // if (quantityOn) {
    //     console.log("loadFromSavedValue",loadFromSavedValue);
    // }

    if (quantity) {
        useEffect(() => {
            if ((loadFromSavedValue) && ((typeof loadFromSavedValue.quantity==="number") && (loadFromSavedValue.quantity>1))) {
                quantityValueToLoadRef.current = {
                    custom: (loadFromSavedValue.quantity>10),
                    value: loadFromSavedValue.quantity,
                    valueDisplay: (loadFromSavedValue.quantity>10)?loadFromSavedValue.quantity.toString():""
                };
                quantityOn.value = true;
            }
        },[loadFromSavedValue]);        
    }
    
    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <OptionTitle caption={title} subCaption={subTitle} quantity={quantity} quantityOn={quantityOn} />
                <div className="buttons">
                    {buttons.map(({caption,custom,customRange,customDisplay,customPlaceholder,id,slideshowMatch},index) => 
                        <Button 
                            key={index} 
                            id={id}
                            text={caption} 
                            custom={custom}
                            minWidth={minWidth}
                            selected={selectedIndex.value===index} 
                            unselected={(selectedIndex.value!==-1) && (selectedIndex.value!==index)}
                            onSelected={(auto?: boolean) => {
                                selectedIndex.value = index;
                                if (slideshowMatch) {
                                    onSlideShowMatch(slideshowMatch,auto);
                                }
                                if (!custom) {
                                    onValue({
                                        type: "buttons",
                                        id,
                                        ...((quantity && quantityOn.value)?{quantity: quantityValue.value.value}:{})
                                    },auto);
                                }
                            }}
                            onCustomValue={(value: number,auto?: boolean) => {
                                onValue({
                                    type: "buttons",
                                    id,
                                    value
                                },auto);
                            }}
                            customRange={customRange}
                            customDisplay={customDisplay}
                            customPlaceholder={customPlaceholder}
                            loadFromSavedValue={loadFromSavedValue}
                        />
                    )}
                </div>
                {quantity?(
                    <div className="quantity" style={{
                        height: quantityOn.value?`${quantityWrapperHeight}rem`:"0rem"
                    }}>
                        <div ref={quantityWrapperRef} className="quantity-wrapper">
                            <QuantitySelect 
                                excludeZero
                                value={quantityValue}
                            />
                        </div>
                    </div>
                ):""}                
            </div>
            <style jsx>{`
            .container {
            }
            .buttons {}
            .quantity {
                --duration: 0.3s;                
                overflow-y: hidden;
                transition: height var(--duration);
            }
            .quantity-wrapper {
                width: 80rem;
                padding-top: 10rem;
                opacity: 0;
                transition: opacity 0.8s;
            }
            .quantity-on .quantity-wrapper {
                opacity: 1;
            }
            `}</style>
        </>
    );
}