import { forwardRef, MutableRefObject, useContext, useEffect, useImperativeHandle, useRef } from "react";
import { EventPane } from "../../core/event_pane";
import SvgButton from "../../core/svg_button";
import { useCssStateManager } from "../../../hooks/use_css_state_manager";
import { useSmartState } from "../../../hooks/use_smart_state";
import { ConfigProductData } from "../../../lib/prebuild-data/types";
import { useResizeObserver } from "../../../hooks/use_resize_observer";
import { trackTouchMoveDelta } from "../../../lib/touch_move_tracker";
import { Image } from "../../common/scaled-next-image";
import { LayoutStateContext } from "../../../contexts/layout_state";
import { prebuildData } from "../../../lib/prebuild-data";

export type ProductGalleryProps = {
    product: ConfigProductData;
    onFrameResize?: (height: number) => void;
};

function FillImage({src}: {src: string}) {
    const cssStateManager = useCssStateManager(["container"]);
    const show = cssStateManager.useState(false,"show");

    const loadHandler = () => {
        show.value = true;
    };

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <Image src={src} layout="fill" onLoad={loadHandler} />
            </div>
            <style jsx>{`
            .container {
                position: absolute;
                left: 0rem;
                top: 0rem;
                width: 100%;
                height: 100%;
                opacity: 0;
                transition: opacity 1s;
            }
            .container.show {
                opacity: 1;
            }
            `}</style>
        </>
    );
}

function GradualFillImage({smallSrc,largeSrc}: {smallSrc: string;largeSrc: string}) {
    const { scalePixelToRem } = useContext(LayoutStateContext);
    const cssStateManager = useCssStateManager(["container","gradual"]);
    const show = cssStateManager.useState(false,"show");
    const showLarge = cssStateManager.useState(false,"show-large");
    const loadLarge = useSmartState(false);

    const ref = useRef<HTMLDivElement>();

    const loadHandler = () => {
        show.value = true;
        if (ref.current) {
            if (scalePixelToRem(ref.current.offsetHeight)>450) {
                loadLarge.value = true;
            }
        }        
    };

    const loadLargeHandler = () => {
        showLarge.value = true;
    };

    return (
        <>
            <div className={cssStateManager.getClassNames()} ref={ref}>
                <div className="image-frame">
                    <Image src={smallSrc} layout="fill" onLoad={loadHandler} />
                </div>
                {loadLarge.value?(
                    <div className="image-frame large-image">
                        <Image src={largeSrc} layout="fill" onLoad={loadLargeHandler} />
                    </div>
                ):""}
            </div>
            <style jsx>{`
            .container {
                position: absolute;
                left: 0rem;
                top: 0rem;
                width: 100%;
                height: 100%;
                opacity: 0;
                transition: opacity 1s;
            }
            .container.show {
                opacity: 1;
            }

            .image-frame {
                position: absolute;
                left: 0rem;
                top: 0rem;
                width: 100%;
                height: 100%;                
            }

            .large-image {
                opacity: 0;
            }

            .show-large .large-image {
                opacity: 1;
            }
            `}</style>
        </>
    );
}


function SliderPane({smallSrc,largeSrc,visible,position}: {smallSrc: string;largeSrc: string;visible: boolean;position: "left" | "middle" | "right"}) {
    return (
        <>
            <div className={`container ${position} ${visible?"visible":""}`}>
                <GradualFillImage smallSrc={smallSrc} largeSrc={largeSrc} />
            </div>
            <style jsx>{`
                .container {
                    position: absolute;
                    left: 0rem;
                    top: 0rem;
                    height: 100%;
                    width: 100%;
                    visibility: hidden;
                }
                .container.left {
                    transform: translateX(-100%);    
                }
                .container.right {
                    transform: translateX(100%);    
                }
                .container.visible {
                    visibility: visible;
                }
            `}</style>
        </>
    );
}

export type ProductGalleryRefInterface = {
    slideToIndex: (index: number,bypassSlideshowAnimation?: boolean) => void;
};

function _ProductGallery({product,onFrameResize = () => {}}:ProductGalleryProps,ref: MutableRefObject<ProductGalleryRefInterface>)
{
    const { mobileDetect,scaleRemToPixel } = useContext(LayoutStateContext);
    const { widthCutoff: mobileCutoff } = mobileDetect;
    const g = useRef<string>();
    const { largeImages: images,smallImages: thumbs } = product;

    const rawIndexToRealIndex: {[index: number]: number} = {};
    
    if (images.length===2) {
        rawIndexToRealIndex[0] = 0;
        rawIndexToRealIndex[1] = 1;
        rawIndexToRealIndex[2] = 0;
        rawIndexToRealIndex[3] = 1;
    }
    else {
        for(let i=0;i<images.length;i++) rawIndexToRealIndex[i] = i;
    }

    const rawIndexReserves: {[index: number]: number[]} = {};

    Object.keys(rawIndexToRealIndex).forEach((_rawIndex) => {
        const rawIndex = parseInt(_rawIndex);
        const realIndex = rawIndexToRealIndex[rawIndex];
        rawIndexReserves[realIndex] = rawIndexReserves[realIndex] || [];
        rawIndexReserves[realIndex].push(rawIndex);        
    });

    const realIndexToRawIndex = (realIndex: number) => (realIndex===-1)?-1:rawIndexReserves[realIndex].shift();

    const containIndex = (index: number) => (images.length+index)%images.length;

    const viewIndex = useSmartState(0);
    const thumbViewIndexRef = useRef(0);
    const jumpViewIndexRef = useRef(-1);        
    const lineUp = ((images.length>1)?[
        ((jumpViewIndexRef.current!==-1) && (jumpViewIndexRef.current<viewIndex.value))?jumpViewIndexRef.current:containIndex(viewIndex.value-1),
        viewIndex.value,
        ((jumpViewIndexRef.current!==-1) && (jumpViewIndexRef.current>viewIndex.value))?jumpViewIndexRef.current:containIndex(viewIndex.value+1)
    ]:[-1,0,-1])
        .map(realIndexToRawIndex);

    const bypassAnimationRef = useRef(false);

    const cssStateManager = useCssStateManager(["container"]);
    cssStateManager.useProperty(bypassAnimationRef.current,"bypass-animation");    
    const slideToLeft = useSmartState(false);
    const slideToRight = useSmartState(false);
    // const slideToLeft = cssStateManager.useState(false,"slide-to-left");
    // const slideToRight = cssStateManager.useState(false,"slide-to-right");
    const disableTransition = cssStateManager.useState(false,"disable-transition");    
    cssStateManager.useProperty(images.length===1,"single-image");

    const sliderTouchOffset = useSmartState(0);

    const transitionEndCbRef = useRef<() => void>();

    const transitionHasEnded = () => {
        if (transitionEndCbRef.current) {
            const saved = transitionEndCbRef.current;
            transitionEndCbRef.current = undefined;
            saved();
        }
        bypassAnimationRef.current = false;
    };

    const setSlidePosition = (index: number,cb: () => void = () => {}) => {
        transitionEndCbRef.current = cb;
        slideToLeft.value = index===-1;
        slideToRight.value = index===1;
    };

    const sliderButtonHeight = 34;
    const sliderButtonPadding = 15;
    const sliderButtonOpacity = 0.2;
    const sliderButtonOpacityHoverFactor = 1.45;
    const sliderButtonOuterShift = 48;
    const thumbSize = 63.5;
    const thumbSpacer = 20;    

    const thumbsFrameRef = useRef<HTMLDivElement>();

    const scrollThumbToCenter = () => {
        if (thumbsFrameRef.current) {            
            const { current: thumbsFrameElem } = thumbsFrameRef;
            const thumbSizePixels = scaleRemToPixel(thumbSize);
            const thumbSpacerPixels = scaleRemToPixel(thumbSpacer);
            const frameWidth = thumbsFrameElem.offsetWidth;
            const thumbOffset = thumbViewIndexRef.current*(thumbSizePixels+thumbSpacerPixels);
            const thumbOffsetCenter = thumbOffset+thumbSizePixels/2;
            const idealScrollLeft = thumbOffsetCenter-frameWidth/2;

            thumbsFrameElem.scrollTo({
                left: idealScrollLeft,
                behavior: "smooth"
            });

        }
    };

    const slideToNext = () => {
        if (images.length===1) return;
        const newViewIndex = (viewIndex.value+1)%images.length;
        thumbViewIndexRef.current = newViewIndex;
        scrollThumbToCenter();
        setSlidePosition(1,() => {
            disableTransition.value = true;
            viewIndex.value = newViewIndex;
            setSlidePosition(0);
        });
    };

    const slideToPrevious = () => {
        if (images.length===1) return;
        const newViewIndex = (viewIndex.value===0)?images.length-1:viewIndex.value-1;
        thumbViewIndexRef.current = newViewIndex;
        scrollThumbToCenter();
        setSlidePosition(-1,() => {
            disableTransition.value = true;            
            viewIndex.value = (viewIndex.value===0)?images.length-1:viewIndex.value-1;
            setSlidePosition(0);
        });
    };    

    const slideToIndex = (index: number,bypassSlideshowAnimation?: boolean) => {
        if (images.length===1) return;
        if (index===viewIndex.value) return;
        if (bypassSlideshowAnimation) {
            bypassAnimationRef.current = true;
        }
        const nextIndex = (viewIndex.value+1)%images.length;
        const previousIndex = (viewIndex.value===0)?images.length-1:viewIndex.value-1;
        if ((nextIndex===index) && (previousIndex===index)) {
            if (index===0) {
                slideToPrevious();    
            }
            else {
                slideToNext();    
            }
        }
        else if (nextIndex===index) {
            slideToNext();
        }
        else if (previousIndex===index) {
            slideToPrevious();
        }
        else {            
            jumpViewIndexRef.current = index;
            thumbViewIndexRef.current = index;
            scrollThumbToCenter();
            setSlidePosition((index>viewIndex.value)?1:-1,() => {
                disableTransition.value = true;
                jumpViewIndexRef.current = -1;
                viewIndex.value = index;                    
                setSlidePosition(0);
            });        
        }        
    };

    useImperativeHandle(ref,() => ({
        slideToIndex
    }));

    useEffect(() => {
        if (disableTransition.value) {
            disableTransition.value = false;
        }
    },[disableTransition.value])

    const imageIndexes: number[] = [];
    for(let i=0;i<images.length+1;i++) imageIndexes.push(i);

    const frameRef = useResizeObserver(({height}) => {
        onFrameResize(height);
    },{initialCall: true});

    const touchPaneRef = useRef<HTMLDivElement>();    
    const sliderRef = useRef<HTMLDivElement>();

    const sliderTransitionAdjustment = useSmartState(false); 

    useEffect(() => {
        return trackTouchMoveDelta(touchPaneRef.current,({wx}) => {
            if (sliderRef.current) {
                sliderRef.current.style.transition = "transform 0s";
                sliderRef.current.style.transform = `translateX(${wx}rem)`;
            }
        },{
            onStart: () => {
            },onEnd: ({wx}) => {
                if (sliderRef.current) {
                    const sliderWidth = sliderRef.current.offsetWidth;
                    const thresholdPerc = 20;
                    const division = 100/thresholdPerc;
                    if (wx<=(sliderWidth*-1)/division) {
                        slideToNext();
                    }
                    else if (wx>=sliderWidth/division) {
                        slideToPrevious();
                    }
                    sliderTransitionAdjustment.value = !sliderTransitionAdjustment.value;
                }
            },
            deltaDirection: "Horizontal",
            preventDefaultOnDelta: true
        });
    },[]);
    
    const sliderTranslateXPercNormal = 0+(sliderTransitionAdjustment.value?0.0000001:0);
    const sliderTranslateXPerc = slideToLeft.value?100:(slideToRight.value?-100:sliderTranslateXPercNormal);
    const sliderTransitionDurationNormal = 0.3+(sliderTransitionAdjustment.value?0.0000001:0);
    const sliderTransitionDuration = disableTransition.value?0:(bypassAnimationRef.current?0.0000001:sliderTransitionDurationNormal);

    return (
        <>
            <div className={cssStateManager.getClassNames()} ref={frameRef} >
                <div className="overflow-hidden-wrapper">
                    <div 
                        className="slider" 
                        ref={sliderRef}
                        onTransitionEnd={(e) => {
                            if (e.propertyName==="transform") {
                                transitionHasEnded();
                            }
                        }}
                        style={{
                            transform: `translateX(${sliderTranslateXPerc}%)`,
                            transition: `transform ${sliderTransitionDuration}s`
                        }}
                    >
                        {Object.keys(rawIndexToRealIndex).map((_rawIndex) => {
                            const rawIndex = parseInt(_rawIndex);                            
                            return <SliderPane 
                                key={rawIndex} 
                                smallSrc={prebuildData.bustCache(thumbs[rawIndexToRealIndex[rawIndex]].src)}
                                largeSrc={prebuildData.bustCache(images[rawIndexToRealIndex[rawIndex]].src)}
                                position={(lineUp[0]===rawIndex)?"left":((lineUp[2]===rawIndex)?"right":"middle")} 
                                visible={lineUp.indexOf(rawIndex)!==-1} 
                            />;
                        })}
                    </div>
                </div>
                <div className="slider-button slider-button-left">
                    <SvgButton 
                        src={prebuildData.bustCache("/images/product/gallery/left-arrow.svg")} 
                        aspectRatio={{width: 120,height: 188}} 
                        height={sliderButtonHeight}
                        paddingHori={sliderButtonPadding}
                        paddingVert={sliderButtonPadding}
                        opacity={sliderButtonOpacity}
                        opacityHover={sliderButtonOpacity*sliderButtonOpacityHoverFactor}
                        onClick={slideToPrevious}
                    />
                </div>
                <div className="slider-button slider-button-right">
                    <SvgButton 
                        src={prebuildData.bustCache("/images/product/gallery/right-arrow.svg")} 
                        aspectRatio={{width: 120,height: 188}} 
                        height={sliderButtonHeight}
                        paddingHori={sliderButtonPadding}
                        paddingVert={sliderButtonPadding}
                        opacity={sliderButtonOpacity}
                        opacityHover={sliderButtonOpacity*sliderButtonOpacityHoverFactor}
                        onClick={slideToNext}
                    />
                </div>
                <div className="touch-pane" ref={touchPaneRef}>

                </div>
                {/*}
                <EventPane onClick={() => slideToNext()} />
                {*/}
            </div>
            <div className={`thumbs ${(images.length===1)?"single-image":""}`}>
                <div className="thumbs-frame" ref={thumbsFrameRef} >
                    {thumbs.map(({src},index) => (
                        <div key={index} className={`thumb-wrapper ${(index===0)?"first":""} ${(index===thumbViewIndexRef.current)?"selected":""}`}>
                            <div className="thumb-image-wrapper">
                                <FillImage src={prebuildData.bustCache(src)} />
                            </div>                            
                            <div className="outline"></div>
                            <EventPane onClick={() => slideToIndex(index)} />
                        </div>
                    ))}
                </div>
            </div>
            <style jsx>{`
            .thumbs {
                position: relative;
                height: ${thumbSize}rem;
                width: 100%;
                overflow: hidden;
                margin-top: 10rem;
            }
            .thumbs.single-image {
                display: none;
            }
            .thumbs-frame {
                position: absolute;
                left: 0rem;
                top: 0rem;
                width: 100%;
                height: calc(100% + 50rem);
                overflow-x: auto;
                overflow-y: hidden;
                white-space: nowrap;
            }
            .thumb-wrapper {
                width: ${thumbSize}rem;
                height: ${thumbSize}rem;
                margin-left: ${thumbSpacer}rem;                
                display: inline-block;
                position: relative;
            }
            .thumb-wrapper.first {
                margin-left: 0rem;                
            }
            .thumb-wrapper .outline {
                position: absolute;
                left: 0rem;
                top: 0rem;
                width: 100%;
                height: 100%;
                border: 2rem solid rgba(0,0,0,0.25);
                opacity: 0;
                transition: opacity 0.3s;
            }
            .thumb-wrapper.selected .outline {
                opacity: 1;
            }
            .thumb-image-wrapper {
                position: absolute;
                --padding: 4rem;
                left: var(--padding);
                top: var(--padding);
                right: var(--padding);
                bottom: var(--padding);
            }

            `}</style>                
            <style jsx>{`                
            .container {
                position: relative;
                width: 100%;
                padding: 50%;
            }
            .overflow-hidden-wrapper {
                position: absolute;
                overflow: hidden; 
                left: 0rem;
                top: 0rem;
                width: 100%;           
                height: 100%;
            }
            .slider {
                position: absolute;
                left: 0rem;
                top: 0rem;
                width: 100%;
                height: 100%;              
                transform: translateX(0rem);
                /* transition: transform 0.3s; */               
            }
            .slider-wrapper {
                position: absolute;
                left: 0rem;
                top: 0rem;
                width: 100%;
                height: 100%;             
            }
            .bypass-animation .slider {
                /* transition-duration: 0.0000001s; */
            }
            .slider-pane {
                position: absolute;
                left: 0rem;
                top: 0rem;
                height: 100%;
                width: 100%;
            }
            .slider-pane1 {
                transform: translateX(-100%);
            }
            .slider-pane2 {
            }
            .slider-pane3 {
                transform: translateX(100%);
            }

            /*
            .slide-to-left .slider {
                transform: translateX(100%);
            }

            .slide-to-right .slider {
                transform: translateX(-100%);
            }
            */

            .disable-transition .slider {
                /* transition-duration: 0s; */
            }
            .slider-button {                
                position: absolute;
                top: calc(50% - ${(sliderButtonHeight+sliderButtonPadding*2)/2}rem);                
            }
            .slider-button-left {
                left: -${sliderButtonOuterShift}rem;
            }
            .slider-button-right {
                right: -${sliderButtonOuterShift}rem;
            }
            .single-image .slider-button {
                display: none;
            }

            .touch-pane {
                    display: block;
                    position: absolute;
                    left: 0rem;
                    top: 0rem;
                    right: 0rem;
                    bottom: 0rem;
                }


            @media screen and (max-width: ${mobileCutoff}px) {
                .slider-button {
                    display: none;
                }

                .touch-action-none .touch-pane {
                    /* touch-action: none; */
                }
            }            
            `}</style>            
        </>
    );
}

const ProductGallery = forwardRef(_ProductGallery);

export default ProductGallery;