import { useEffect, useRef } from "react";
import { useCssStateManager } from "../../../../hooks/use_css_state_manager";
import { useSmartState } from "../../../../hooks/use_smart_state";
import { prebuildData } from "../../../../lib/prebuild-data";
import { OptionValueButton } from "../../../../lib/services/cart/types";
import { EventPane } from "../../../core/event_pane";
import OptionTitle from "./commons/title";

export type ColorEntry = {
    color: string;
    id: string;
    caption: string;
    slideshowMatch?: string;
};

export type ColorPickerOptionsProps = {
    title: string;
    subTitle?: string;
    colors: ColorEntry[];
    onSlideShowMatch?: (value: string,bypassSlideshowAnimation?: boolean) => void;
    size?: number;
    initialSelectedItemIndex: number;
    onValue?: (value: OptionValueButton,auto?: boolean) => void;
    loadFromSavedValue?: OptionValueButton;
    price?: number;
};

type ColorProps = {
    id: string;
    color: string;
    selected: boolean;
    onSelect?: (auto?: boolean) => void;
    onStartHover?: () => void;
    onEndHover?: () => void;
    size?: number;
    loadFromSavedValue?: OptionValueButton;
};

function Color({
        id,
        color,
        selected,
        onSelect = () => {},
        onStartHover = () => {},
        onEndHover = () => {},
        size = 35,
        loadFromSavedValue
    }:ColorProps) {
    const padding = 5;    
    const cssStateManager = useCssStateManager(["container"]);
    cssStateManager.useProperty(selected,"show");
    cssStateManager.useProperty(color==="none","slashed");

    useEffect(() => {
        if (loadFromSavedValue) {
            if (loadFromSavedValue.id===id) {
                onSelect(true);
            }
        }
    },[loadFromSavedValue]);    

    return (<>
        <div className={cssStateManager.getClassNames()} style={{backgroundColor: (color==="none")?"#fff":color}}>
            <div className="outline"></div>
            <div className="slash"></div>
            <EventPane onClick={onSelect} onStartHover={onStartHover} onEndHover={onEndHover} overlapHori={7} />
        </div>
        <style jsx>{`
            .container {
                display: inline-block;
                --size: ${size}rem;
                width: var(--size);
                height: var(--size);
                border-radius: var(--size);
                box-shadow: inset 0rem 3rem 12rem -5rem rgb(0 0 0 / 51%);
                margin-right: 14rem;
                margin-bottom: 6rem;
                position: relative;
            }
            .outline {
                position: absolute;
                --padding: -${padding}rem;
                left: var(--padding);
                top: var(--padding);
                right: var(--padding);
                bottom: var(--padding);
                border-radius: ${size}rem;                
                opacity: 0;
                transition: opacity 0.3s;
            }
            .slash {
                --height: 5rem;
                position: absolute;
                left: 0rem;
                right: 0rem;
                top: calc(50% - (var(--height) / 2));
                height: var(--height);
                background-color: rgba(0,0,0,0.06);
                border-radius: 3rem;
                transform: rotate(-45deg);
                display: none;
            }
            .slashed .slash {
                display: block;
            }
            .hover .outline {
                opacity: 1;
                border: 1rem solid #0000002a;
            } 
            .show .outline {
                opacity: 1;
                border: 2rem solid #9ba2fb;
            }
        `}</style>
    </>);
}

export default function ColorPickerOption({
        colors,
        title,
        subTitle = "",
        onSlideShowMatch = () => {},
        size,
        initialSelectedItemIndex,
        onValue = () => {},
        loadFromSavedValue,
        price
    }:ColorPickerOptionsProps)
{
    const cssStateManager = useCssStateManager(["container"]);
    const selectedIndex = useSmartState(initialSelectedItemIndex);

    const permanentFooterText = (selectedIndex.value!==-1)?colors[selectedIndex.value].caption:"";
    const hoverFooterText = useSmartState("");

    const footerText = hoverFooterText.value || permanentFooterText;

    const { productOptions: { colorPickerChoiceTextColor,colorPickerHoverTextColor } } = prebuildData.getActiveTheme();    

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <OptionTitle caption={title} subCaption={subTitle} price={price} priceEnabled={colors[selectedIndex.value]?.id!=="none"} />
                <div className="colors">
                    {colors.map(({color,id},index) => <Color 
                            key={index} 
                            id={id}
                            color={color} 
                            size={size} 
                            selected={selectedIndex.value===index} 
                            onSelect={(auto = false) => {
                                selectedIndex.value = index;
                                if (colors[index].slideshowMatch) {
                                    onSlideShowMatch(colors[index].slideshowMatch,!!auto);
                                }
                                onValue({
                                    type: "buttons",
                                    id
                                },auto);                        
                            }}  
                            onStartHover={() => {
                                hoverFooterText.value = colors[index].caption;
                            }}
                            onEndHover={() => {
                                hoverFooterText.value = "";
                            }}
                            loadFromSavedValue={loadFromSavedValue}
                        />)}
                </div>
                <div className={`footer ${((hoverFooterText.value!=="") && (hoverFooterText.value!==permanentFooterText))?"hovering":""}`}>{footerText}</div>
            </div>
            <style jsx>{`
            .container {
                padding-bottom: 0rem;
            }
            .footer {
                height: 24rem;
                font-size: 15rem;
                margin-top: 5rem;
                color: ${colorPickerChoiceTextColor};            
            }
            .hovering {
                color: ${colorPickerHoverTextColor};
            }
            `}</style>
        </>
    );
}