import { useContext, useEffect } from "react";
import { LayoutStateContext } from "../../../../contexts/layout_state";
import { useCssStateManager } from "../../../../hooks/use_css_state_manager";
import { useSmartState } from "../../../../hooks/use_smart_state";
import { prebuildData } from "../../../../lib/prebuild-data";
import { OptionValueSelect, OptionValueVariantSelect } from "../../../../lib/services/cart/types";
import OptionTitle from "./commons/title";

export type SelectEntry = {
    caption: string;    
    value: string;
};

export type SelectComponentProps = {
    title: string;
    subTitle?: string;
    unselectedCaption?: string;
    removable?: boolean;
    entries: SelectEntry[];
    description?: string;
    onValue?: (value: OptionValueSelect | OptionValueVariantSelect ) => void;
    valueType?: "select" | "variant-select";
    loadFromSavedValue?: OptionValueSelect | OptionValueVariantSelect;
    price?: number;
};

export default function SelectComponent({
        title,
        subTitle = "",
        entries,
        unselectedCaption = "",
        removable = false,
        description = "",
        valueType = "select",
        onValue = () => {},
        loadFromSavedValue,
        price
    }:SelectComponentProps)
{
    const { mobileDetect } = useContext(LayoutStateContext);
    const { smallDesktopWidthCutoff: mobileCutoff } = mobileDetect;

    const unselectedValue = "--unselected--";    
    const removeValue = "--remove--";
    const selectedValue = useSmartState(((unselectedCaption!=="")?unselectedValue:null) || entries?.[0].value || "");

    const cssStateManager = useCssStateManager(["container"]);    
    const unselected = (unselectedCaption!=="") && (selectedValue.value===unselectedValue);
    cssStateManager.useProperty(unselected,"unselected");

    const selectChangeHandler = (value: string) => {        
        const newValue = (value===removeValue)?(((unselectedCaption!=="")?unselectedValue:null) || entries?.[0].value || ""):value;
        selectedValue.value = newValue;
        if (valueType==="select") {            
            onValue((newValue===unselectedValue)?null:{
                type: "select",
                id: newValue
            });
        }
        else if (valueType==="variant-select") {
            onValue((newValue===unselectedValue)?null:{
                type: "variant-select",
                id: newValue
            });
        }
    };

    useEffect(() => {
        if (loadFromSavedValue) {
            selectChangeHandler(loadFromSavedValue.id);
        }
    },[loadFromSavedValue]);

    const { productOptions: { buttonSelectedTextColor,buttonBackgroundColor,lightTextColor,buttonUnselectedTextColor } } = prebuildData.getActiveTheme();

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <OptionTitle caption={title} subCaption={subTitle} price={price} priceEnabled={selectedValue.value!==unselectedValue} />
                <select className="select" value={selectedValue.value} onChange={(e) => selectChangeHandler(e.target.value)}>
                    {(unselectedCaption!=="")?<option className="hidden-option" value={unselectedValue}>{unselectedCaption}</option>:""}
                    {(removable && !unselected)?<option value={removeValue}>-- Remove ---</option>:""}
                    {entries.map(({caption,value},index) => <option key={index} value={value}>{caption}</option>)}
                </select>
                {(description!=="")?(
                    <div className="description">
                        {description}
                    </div>
                ):""}
            </div>
            <style jsx>{`
            .container {
                padding-bottom: 16rem;
            }
            .select {
                cursor: pointer;
                height: 45rem;
                outline: none;
                border: 1rem solid rgba(190, 190, 190);
                border-radius: 5rem;
                width: 100%;
                font-size: 16rem;
                font-family: "Open Sans", "Arial", "Helvetica", sans-serif;
                padding: 0rem 9rem;
                color: ${buttonSelectedTextColor};
                /* background-color: transparent; */
                background-color: ${buttonBackgroundColor};
                font-weight: 600;                            
            }

            .description {
                color: ${lightTextColor};
                font-size: 12rem;
                margin-top: 15rem;                
            }

            .unselected .select {
                color: ${buttonUnselectedTextColor};
            }

            .select option {
                color: ${buttonSelectedTextColor};
            }

            .hidden-option {
                display: none;
            }

            @media screen and (max-width: ${mobileCutoff}px) {
                .select {
                    background-color: var(--body-background-color);
                }
            }
            `}</style>
        </>
    );
}