import { misc } from "../../lib/misc";
import { Image } from "../common/scaled-next-image";
import { EventPane } from "./event_pane";

export type SvgButtonProps = {
    src: string;
    aspectRatio: {width: number;height: number};
    height?: number;
    padding?: number;
    paddingVert?: number;
    paddingHori?: number;
    filter?: string;
    filterHover?: string;
    filterTouchdown?: string;
    animationDuration?: number;
    onClick?: () => void;
    opacity?: number;
    opacityHover?: number;
    opacityTouchdown?: number;
};

export default function SvgButton({
    src,
    aspectRatio,
    height = aspectRatio.height,
    padding = 0,
    paddingVert = padding,
    paddingHori = padding,
    filter,
    filterHover = filter,
    filterTouchdown = filterHover,
    animationDuration = 0.3,
    opacity = 1,
    opacityHover = opacity,
    opacityTouchdown = opacity,
    onClick = () => {}
}:SvgButtonProps)
{
    const imgSize = misc.createImageDimensions(aspectRatio.width,aspectRatio.height,height);

    return (
        <>
            <div className="container" style={{padding: `${paddingVert}rem ${paddingHori}rem`}}>
                <div className="image-wrapper" style={{width: `${imgSize.width}rem`,height: `${imgSize.height}rem`}}>
                    <Image className="normal" src={src} layout="fill" />
                    <Image className="hover" src={src} layout="fill" />
                    <Image className="touchdown" src={src} layout="fill" />
                </div>
                <EventPane onClick={onClick} touchDownOnMouseDown />
            </div>
            <style jsx>{`
            .container {                
                display: inline-block;
            }
            .image-wrapper {
                position: relative;
            }
            .image-wrapper img:global(.normal) {
                ${filter?`filter: ${filter};`:""}
                opacity: ${opacity};
                transition: opacity ${animationDuration}s;
            }
            .image-wrapper img:global(.hover) {
                ${filterHover?`filter: ${filterHover};`:""}
                opacity: 0;
                transition: opacity ${animationDuration}s;
            }
            .image-wrapper img:global(.touchdown) {
                ${filterTouchdown?`filter: ${filterTouchdown};`:""}
                opacity: 0;
                transition: opacity ${animationDuration}s;
            }
            .container.hover .image-wrapper img:global(.hover) {
                opacity: ${opacityHover};
            }            
            .container.touchdown .image-wrapper img:global(.touchdown) {
                opacity: ${opacityTouchdown};
            }
            .container.hover .image-wrapper img:global(.normal) {
                opacity: 0;
            }
            .container.touchdown .image-wrapper img:global(.normal) {
                opacity: 0;
            }
            `}</style>
        </>
    );
}