import { useContext, useEffect, useRef } from "react";
import { LayoutStateContext } from "../../../contexts/layout_state";
import { useCssStateManager } from "../../../hooks/use_css_state_manager";
import { ProductReview, ProductReviewSummary, useProductReviewSummary } from "../../../hooks/use_product_reviews_summary";
import { consumeReviewsHeader } from "../../../hooks/use_reviews_header";
import { SmartState, useSmartState } from "../../../hooks/use_smart_state";
import { apiReview, ReviewsOrder } from "../../../lib/api/review";
import { misc } from "../../../lib/misc";
import { prebuildData } from "../../../lib/prebuild-data";
import { ConfigProduct } from "../../../scripts/pre-build/types";
import CenteredSpinner from "../../common/centered-spinner";
import StarRating from "../../common/star_rating";
import { EventPane } from "../../core/event_pane";
import { Icon } from "../../core/icon";
import Layout from "../../layout";

export type ReviewBarProps = {
    starCount: number;
    percentage: number;
    reviewCount: number;
    reviewCountTextWidth: number;
    selected?: boolean;
    notSelected?: boolean;
    onClick?: () => void;
};

const reviewBarFontSize = 14;

function ReviewBar({starCount,percentage,reviewCount,reviewCountTextWidth,selected,notSelected,onClick = () => {}}:ReviewBarProps)
{
    const cssStateManager = useCssStateManager(["review-bar"]);    
    cssStateManager.useProperty(reviewCountTextWidth===-1,"hidden");
    cssStateManager.useProperty(selected,"selected");    
    cssStateManager.useProperty(notSelected,"not-selected");    

    const overlapHori = 16;
    const overlapVert = 5;

    const { productReviews: { 
        reviewStarsBarHoverTextColor,
        reviewStarsBarHighlighterColor,
        reviewStarsBarBorderColor,
        reviewStarsBarPercentageBarColor,
        reviewStarsBarPercentageBarHoverColor,
        reviewStarsBarPercentageBarBorderColor,
        reviewStarsBarPercentageBarHoverBorderColor,
        reviewStarsBarPercentageBarSelectedColor,
        ratingFilterBadgeCrossColor,
        lightTextColor
    } } = prebuildData.getActiveTheme();    

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <div className="back-highlighter"></div>
                <div className="star-count">{starCount} star</div>
                <div className="percentage-bar">
                    <div className="percentage-bar-inner" style={{width: `${percentage}%`}}></div>
                </div>
                <div className="stats-percentage">{percentage}%</div>
                <div className="stats-review-count" style={{
                    flex: `0 0 ${reviewCountTextWidth+14}rem`
                }}>({reviewCount})</div>
                <EventPane overlapHori={overlapHori} overlapVert={overlapVert} onClick={onClick} />
            </div>
            <style jsx>{`
            .review-bar {
                display: flex;
                flex-direction: row;
                font-size: ${reviewBarFontSize}rem;
                margin-bottom: 8rem;   
                position: relative;             
                align-items: center;
                opacity: 1;
                transition: color var(--duration),opacity var(--duration);
            }

            .review-bar.not-selected {
                opacity: 0.6;
            }
            .review-bar.selected,.review-bar.hover {
                color: ${reviewStarsBarHoverTextColor}
            }
            .back-highlighter {
                opacity: 0;                
                position: absolute;
                --padding-hori: ${overlapHori}rem;
                --padding-vert: ${overlapVert}rem;
                left: calc(var(--padding-hori) * -1);
                right: calc(var(--padding-hori) * -1);
                top: calc(var(--padding-vert) * -1);
                bottom: calc(var(--padding-vert) * -1);
                background-color: ${reviewStarsBarHighlighterColor};
                z-index: -1;
                border: 1rem solid ${reviewStarsBarBorderColor};
                transition: opacity var(--duration);
            }
            .selected .back-highlighter {
                opacity: 1;
            }
            .hidden {
                visibility: hidden;
            }

            .star-count {
                flex: 0 0 52rem;     
                margin: 3rem 0rem;
            }

            .percentage-bar {
                flex: 1 1;
                border: 1rem solid ${reviewStarsBarPercentageBarBorderColor};
                border-radius: 3rem;
                height: 19rem;
            }

            .percentage-bar-inner {
                background-color: ${reviewStarsBarPercentageBarColor};
                height: 100%;
                transition: background-color var(--duration);
            }

            .review-bar.hover .percentage-bar-inner {
                background-color: ${reviewStarsBarPercentageBarHoverColor};
            }

            .review-bar.hover .percentage-bar {
                border: 1rem solid ${reviewStarsBarPercentageBarHoverBorderColor};
            }

            .selected .percentage-bar-inner,.review-bar.hover .percentage-bar-inner {
                background-color: ${reviewStarsBarPercentageBarSelectedColor};
            }

            .stats-percentage {
                text-align: right;
                flex: 0 0 43rem;
            }

            .stats-review-count {
                text-align: right;
                color: ${ratingFilterBadgeCrossColor};
            }

            .selected .stats-review-count,.review-bar.hover .stats-review-count {
                color: ${lightTextColor};
            }
            `}</style>
        </>
    );
}

export type ReviewItemProps = {
    review: ProductReview;
    first: boolean;
};

function ReviewItem({review: { title,body,name,rating,timestamp },first}:ReviewItemProps)
{
    const { mobileDetect } = useContext(LayoutStateContext);
    const { isSmallDesktopScreen: isMobile } = mobileDetect;

    const timestampToDateDisplay = (timestamp: number) => {
        const date = new Date(timestamp);        
        const desktopMonthTable = ["January","February","March","April","May","June","July","August","September","October","November","December"];
        const mobileMonthTable = ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec"];
        const monthTable = isMobile.current?mobileMonthTable:desktopMonthTable;
        return `${monthTable[date.getMonth()]} ${date.getDate()}, ${date.getFullYear()}`;
    };

    const cssStateManager = useCssStateManager(["review-item"]);
    cssStateManager.useProperty(first,"first");

    const { productReviews: {
        reviewItemTextColor,
        reviewItemDateTextColor,
        reviewItemAvatarBackgroundColor
    } } = prebuildData.getActiveTheme();

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <div className="identity">
                    <div className="avatar">{name.trim().charAt(0).toUpperCase()}</div>
                    <div className="avatar-right">
                        <div className="rating">
                            <StarRating rating={rating} scale={0.9} />                            
                            <span className="date">{timestampToDateDisplay(timestamp)}</span>
                        </div> 
                        <div className="name">{name}</div>   
                    </div>
                </div>
                <div className="title-body">
                    {title?(
                        <div className="title">{title}</div>
                    ):""}                    
                    <div className="body">{body}</div>
                </div>
            </div>
            <style jsx>{`
            .review-item {
                margin-top: 40rem;
                color: ${reviewItemTextColor};
            }
            .first {
                margin-top: 0rem;
            }
            .title {
                font-weight: 600;
                margin-bottom: 12rem;
            }
            .title-body {
                margin-top: 15rem;
                font-size: 15rem;
            }
            .body {
                line-height: 25rem;
            }
            .identity {
                display: flex;
                flex-direction: row;
            }
            .avatar {
                --size: 50rem;
                width: var(--size);
                height: var(--size);
                border-radius: var(--size);
                background-color: ${reviewItemAvatarBackgroundColor};
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 13rem;
                font-weight: 600;
            }
            .avatar-right {
                padding-left: 10rem;
            }
            .rating {}

            .rating .date {
                color: ${reviewItemDateTextColor};
                font-size: 13rem;
                position: relative;
                left: 8rem;
                top: -2rem;
            }
            .name {
                font-weight: 600;
                font-size: 15rem;
            }
            `}</style>
        </>
    );
}

export type NoReviewsProps = {
};

