import { MutableRefObject, useContext, useEffect, useRef } from "react";
import { LayoutStateContext } from "../../../contexts/layout_state";
import { useCssStateManager } from "../../../hooks/use_css_state_manager";
import { usePubSubReceiver, usePubSubSender } from "../../../hooks/use_pub_sub";
import { useSmartState } from "../../../hooks/use_smart_state";
import { misc } from "../../../lib/misc";
import { prebuildData } from "../../../lib/prebuild-data";
import PriceDisplay from "../../common/price-display";
import PriceDisplayWithDiscount from "../../common/price-display-with-discount";
import StarRating from "../../common/star_rating";
import PriceUpdate, { PriceUpdateRefInterface } from "./price-update";
import { consumeReviewsHeader } from "../../../hooks/use_reviews_header";

export type PriceTopSliderProps = {
    productId: string;
    title: string;
    price: number;
    topSectionRef: MutableRefObject<HTMLDivElement>;
    titleBadgeRef: MutableRefObject<HTMLDivElement>;
};

export default function PriceTopSlider({productId,title,price,topSectionRef,titleBadgeRef}:PriceTopSliderProps)
{
    const cssStateManager = useCssStateManager(["container"]);
    const miniNavbarDown = usePubSubReceiver<boolean>("mini-navbar-down",false);
    const passedScroll = useSmartState(false);
    const priceTopSliderOpen = miniNavbarDown.value && passedScroll.value;
    cssStateManager.useProperty(priceTopSliderOpen,"open");    

    const { send: sendPriceTopSliderOpen } = usePubSubSender<boolean>("price-top-slider-open");    

    useEffect(() => {
        sendPriceTopSliderOpen(priceTopSliderOpen);
    },[priceTopSliderOpen]);

    const layout = useContext(LayoutStateContext);
    const { page,scaleRemToPixel,mobileDetect } = layout;
    let { reviewsCount,starRating } = consumeReviewsHeader(productId,layout);
    starRating = Math.min(Math.max(Math.round(starRating*2)/2,0),5);

    cssStateManager.useProperty(reviewsCount===0,"no-reviews");

    const { isSmallDesktopScreen: isMobileActive,smallDesktopWidthCutoff: mobileCutoff } = mobileDetect;
    const { dollar,cents } = misc.splitPriceForDisplay(price);
    const discountedPrice = prebuildData.getProductPageDiscountedPrice(productId,price).price;

    const reload = useSmartState(false);

    useEffect(() => 
    {
        return page.addScrollEventListener(({top}) => 
        {
            const getDesktopCutoff = () => {
                const gap = (topSectionRef.current?.offsetHeight-titleBadgeRef.current?.offsetHeight) || 0;
                return gap+scaleRemToPixel(230);
            };
            passedScroll.value = top>(isMobileActive.current?scaleRemToPixel(195):getDesktopCutoff());            
        });
        reload.value = true;
    },[]);

    const { productTitlePriceTopSlider: { backdropFilter,borderBottomColor,mobileborderBottomColor,textColor } } = prebuildData.getActiveTheme();

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <div className="slider">
                    <div className="title">
                        {title}
                        <div className="mobile-stars">
                            <StarRating rating={starRating} scale={0.6} />
                        </div>
                    </div>
                    <div className="stars">
                        <StarRating rating={starRating} scale={0.8} />
                    </div>
                    <div className="spacer"></div>
                    <div className="price-update"><PriceUpdate price={discountedPrice} /></div>
                    <div className="price"><PriceDisplay price={discountedPrice} size={isMobileActive.current?20:23} /></div>
                </div>
            </div>
            <style jsx>{`
            .container {
                --duration: 0.3s;
                position: fixed;
                top: 48rem;
                left: 0rem;
                width: 100%;
                height: 52rem;
                overflow: hidden;
                font-size: 16rem;
                color: ${textColor};
                visibility: hidden;
                transition: visibility var(--duration);
                z-index: 1;
            }
            .no-reviews .mobile-stars,.no-reviews .stars {
                display: none;
            }
            .open.container {
                visibility: visible;
            }
            .slider {
                position: absolute;
                top: 0rem;
                left: 0rem;
                width: 100%;
                height: 100%;
                background-color: var(--body-background-color);
                opacity: 0.97;
                backdrop-filter: ${backdropFilter};
                border-bottom: 1rem solid ${borderBottomColor};
                display: flex;
                align-items: center;
                transform: translateY(-100%);
                transition: transform var(--duration);
            }
            .open .slider {
                transform: translateY(0rem);
            }
            .stars {
                padding-left: 10rem;
                padding-bottom: 3rem;
            }
            .title {
                padding-left: 12rem;                
                font-weight: 600;                
            }
            .spacer {
                flex: 1 1;
            }
            .price {
                padding-right: 10rem;
                padding-bottom: 3.5rem;
                /*
                font-weight: 600;
                font-size: 20rem;
                */
            }
            .price-update {
                margin-right: 8rem;
            }

            .sup {
                font-size: 13rem;
                position: relative;
                top: -6rem;            
            }
            .sup.cents {
                left: 1rem;
            }
            .mobile-stars {
                display: none;
            }
            @media screen and (max-width: ${mobileCutoff}px) {
                .container {
                    height: 46rem;
                    font-size: 14rem;
                }
                .slider {
                    border-bottom: 1rem solid ${mobileborderBottomColor};
                }
                .stars {
                    display: none;
                }               
                .mobile-stars {
                    display: block;
                    margin-top: -1rem;
                }
                .title {
                    font-size: 13rem;
                }
                .price {
                    padding-bottom: 2.5rem;
                }
            }
            `}</style>
        </>
    );
}