export type TitlePriceBadgeProps = {
    editCartItem?: CartItem;
    product: ConfigProduct;
    price: number;
    onReadMoreReviewsClick?: () => void;
    alreadyAddedToCart: boolean;
    alreadyAlreadyAddedToCart: boolean;
    enabled: boolean;
    addToCartActionsRef: MutableRefObject<AddToCartActions>;
    noticeText?: string;
};

type ReviewHighlightProps = {
    product: ConfigProduct;
    onReadMoreReviewsClick?: () => void;
    enabled: boolean;
    hideReview: SmartState<boolean>;
};

type PlaceholderBarProps = {
    height?: number;
    width?: number;
    marginBottom?: number;
    enabled: boolean;
};

function PlaceholderBar({height = 16,width = -1,marginBottom = 0,enabled}:PlaceholderBarProps) {
    return (<>
        <div className="bar" style={{
            height: `${height}rem`,
            width: `${(width===-1)?"100%":`${width}rem`}`,
            marginBottom: `${marginBottom}rem`,
            backgroundColor: enabled?"#e7e7e7":"#efefef"
        }}></div>
        <style jsx>{`
            .bar {
                opacity: 1;
            }
        `}</style>
    </>);
}

function ReviewHighlight({product,onReadMoreReviewsClick = () => {},enabled,hideReview}:ReviewHighlightProps) {
    const cssStateManager = useCssStateManager(["review-highlight"]);    

    const { scalePixelToRem } = useContext(LayoutStateContext);

    const title = useSmartState("");
    const body = useSmartState("");
    const author = useSmartState("");

    const placeholderFlashOn = cssStateManager.useState(false,"placeholder-flash");
    const reviewReady = cssStateManager.useState(false,"review-ready");

    const containerHeight = useSmartState(-1);
    const containerHeightDuration = useSmartState(0);

    useEffect(() => {
        placeholderFlashOn.value = true;
    },[]);

    const reviewRef = useRef<HTMLDivElement>();
    const placeholderRef = useRef<HTMLDivElement>();

    useAsyncEffect(async (isMounted) => {        
        const review = await apiReview.getHighlight(product.id);
        console.log("review",review);
        if (!isMounted()) return;
        if (review) {
            title.value = review.title;
            body.value = review.body;
            author.value = review.name;
            reviewReady.value = true;
            if ((placeholderRef.current) && (reviewRef.current)) {
                const diff = Math.abs(placeholderRef.current.offsetHeight-reviewRef.current.offsetHeight);
                const secondsPerRem = 0.008;
                containerHeightDuration.value = secondsPerRem*scalePixelToRem(diff);
                containerHeight.value = scalePixelToRem(placeholderRef.current.offsetHeight);
                setTimeout(() => {
                    if (reviewRef.current) {
                        containerHeight.value = scalePixelToRem(reviewRef.current.offsetHeight);
                    }                    
                },50);
            }
        }
        else {
            hideReview.value = true;
        }
    },[]);

    const placeholderTransitionEndHandler = (elem: HTMLDivElement,propertyName: string) => {
        if ((elem===placeholderRef.current) && (propertyName==="opacity") && (!reviewReady.value)) {
            placeholderFlashOn.value = !placeholderFlashOn.value;
        }
    };

    const getContainerHeightValue = () => {
        const { value } = containerHeight;
        if (value===-1)
        {
            return "auto";
        }
        else {
            return `${value}rem`;
        }
    };

    const { productPriceTitleBadge: { reviewBodyTextColor,readMoreHoverTextColor,readMoreTextColor } } = prebuildData.getActiveTheme();

    return (<>
        <div className={cssStateManager.getClassNames()} style={{
            height: getContainerHeightValue(),
            transition: `height ${containerHeightDuration.value}s`
        }}>
            <div className="placeholder-frame">
                <div ref={placeholderRef} className="placeholder" onTransitionEnd={(e) => placeholderTransitionEndHandler(e.target as HTMLDivElement,e.propertyName)}>
                    <PlaceholderBar width={160} marginBottom={12} enabled={enabled} />
                    <PlaceholderBar height={12} marginBottom={6} enabled={enabled} />
                    <PlaceholderBar height={12} marginBottom={6} enabled={enabled} />
                    <PlaceholderBar height={12} marginBottom={6} enabled={enabled} />
                    <PlaceholderBar height={12} marginBottom={6} enabled={enabled} />
                    <PlaceholderBar height={12} marginBottom={12} enabled={enabled} />
                    <PlaceholderBar width={90} marginBottom={28} enabled={enabled} />
                    <PlaceholderBar width={80} marginBottom={5} enabled={enabled} />
                    <PlaceholderBar width={15} height={12} marginBottom={12} enabled={enabled} />
                </div>
            </div>
            <div ref={reviewRef} className="review-frame">
                <div className="review">
                    <div className="review-title">{title.value}</div>
                    <div className="review-body">"{body.value}"</div>
                    <div className="review-author">- {author.value}</div>
                </div>
                <div className="see-more-button">
                    <div className="see-more-button-text">Read More</div>
                    <div className="see-more-button-arrow"><i className="icon-navbar-left-arrow"></i></div>
                    <EventPane onClick={onReadMoreReviewsClick} />
                </div>
            </div>
        </div>
        <style jsx>{`
            .review-highlight {
                position: relative;
                --ready-duration: 0.5s;
                overflow-y: hidden;
            }
            .placeholder {
                padding-top: 2rem;
                display: flex;
                flex-direction: column;
                align-items: center;
                opacity: 1;
                transition: opacity 0.6s;
            }
            .placeholder-flash .placeholder {
                opacity: 0.5;
            }
            .placeholder-frame {
                opacity: 1;
                transition: opacity var(--ready-duration);
            }
            .review-ready .placeholder-frame {
                opacity: 0;
            }

            .review-frame {
                position: absolute;
                top: 0rem;
                left: 0rem;
                right: 0rem;
                opacity: 0;
                transition: opacity var(--ready-duration);
            }
            .review-ready .review-frame {
                opacity: 1;
            } 
            .review {
                font-size: 13rem;
                margin-bottom: 6rem;
            }
            .review-title {
                font-weight: 600;
                margin-bottom: 8rem;
            }
            .review-body {
                font-style: italic;
                margin-bottom: 8rem;
                color: ${reviewBodyTextColor};
            }
            .review-author {
                font-weight: 600;
            }
            .see-more-button {
                font-size: 14rem;
                padding: 0rem 20rem;
                padding-top: 20rem;
                color: ${readMoreTextColor};
                position: relative;
                display: inline-block;                
                transition: color 0.3s;
            }
            .see-more-button.hover {
                color: ${readMoreHoverTextColor};
            }
            .see-more-button-text {
                margin-bottom: 2rem;
            }
            .see-more-button-arrow {
                transform: rotate(-90deg);
                font-size: 12rem;
            }
        `}</style>
    </>);
}

function Separator({enabled}: {enabled: boolean}) {
    const cssStateManager = useCssStateManager(["separator"]);
    cssStateManager.useProperty(!enabled,"disabled");

    const { productPriceTitleBadge: { separatorLineColor } } = prebuildData.getActiveTheme();
    return (
        <>
            <div className={cssStateManager.getClassNames()}><div></div></div>
            <style jsx>{`
            .separator {
                display: inline-block;
                width: 85%;
                padding: 20rem 0rem;                
            }
            .separator div {
                height: 1rem;
                background-color: ${separatorLineColor};
                transition: background-color var(--duration);
            }
            .disabled .separator div {
                background-color: ${separatorLineColor};
            }            
            `}</style>
        </>
    );
}

export type IncludesProps = {
    product: ConfigProduct;
    enabled: boolean;
};

export function Includes({product,enabled}:IncludesProps)
{
    const optionsCompiler = prebuildData.getOptionsCompiler();
    
    const { mobileDetect } = useContext(LayoutStateContext);
    const { smallDesktopWidthCutoff: mobileCutoff } = mobileDetect;

    const cssStateManager = useCssStateManager(["includes"]);
    cssStateManager.useProperty(!enabled,"disabled");

    const productOptions = optionsCompiler.getProductOptions(product.id);    
    const includedOptions = productOptions.options.filter((option) => (option.type==="checkbox") && (option.included)) as OptionsOptionCheckbox[];

    const checkboxProductFnCacheRef = useRef<{[id: string]: any}>({});
    const { current: checkboxProductFnCache } = checkboxProductFnCacheRef;

    if (includedOptions.length===0) return null;

    const getCaption = () => {
        const captions = {
            "bundle": "Included in bundle",
            "pack": "Included in pack",
            "other": "Included"
        };
        const lowerCaseCaption = product.caption.toLowerCase();
        return captions[(lowerCaseCaption.indexOf("bundle")!==-1)?"bundle":((lowerCaseCaption.indexOf("pack")!==-1)?"pack":"other")];
    };

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <div className="spacer"></div>
                
                <div className="separator">
                    <Separator enabled={enabled} />                
                </div>
                
                <div className="title"><span>{getCaption()}</span></div>
                {includedOptions.map(({caption,description,product,id},index) => {
                    let productFn = () => ({id: ""});
                    if (product) {                                
                        productFn = (checkboxProductFnCache[id] = checkboxProductFnCache[id] || prebuildData.evalInContext(product));
                    }
                    const productId = productFn()?.id || "";

                    return (
                        <div key={productId} className={`included-checkbox-wrapper ${(index===includedOptions.length-1)?"is-last":""}`}>
                            <CheckboxOption
                                title={caption}
                                titleStyle={"internal"}
                                productId={productId}
                                description={description}
                                included      
                                inBadge
                            />
                        </div>
                    );
                })}
            </div>
            <style jsx>{`
            .includes {                
                margin-left: -12rem;
            }

            .spacer {
                height: 10rem;
            }

            .title {                
                font-size: 13rem;
                padding-bottom:6px;
                margin-top: -36rem;                
                z-index: 1;
                position: relative;                
                text-align: center;                

                /* font-size: 16rem;
                font-weight: 600; */
                /* margin-top: 0rem; */                
                /* margin-top: 24rem; */
                /* text-align: left;                */
                /* color: #666; */

                /* text-align: center; */
            }

            .title span {
                color: #999;
                background-color: #f7f7f7;
                padding: 0rem 12rem;
                transition: background-color var(--duration),color var(--duration);
            }

            .disabled .title span {
                color: #aaa;
                background-color: #fff;
            }

            @media screen and (max-width: ${mobileCutoff}px) {
                .separator {
                    display: none;
                }

                .includes {
                    margin-top: 60rem;
                    margin-bottom: 0rem;
                }

                .title span,.disabled .title span {
                    background-color: transparent;
                }

                .title {
                    text-align: left;
                    font-size: 13rem;
                }
            }
            `}</style>
        </>
    );
}

export type NoticeProps = {
    text?: string;
};

export function Notice({text}:NoticeProps)
{
    const cssStateManager = useCssStateManager(["notice"]);

    if (!text) return null;

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <div className="title">NOTICE</div>
                <span dangerouslySetInnerHTML={{__html: text}}></span>
            </div>
            <style jsx>{`
            .notice {
                border: 2rem solid #000;
                text-align: center;
                padding: 12rem 20rem;
                font-size: 12rem;
                color: #363636;
                margin-top: 20rem;
            }
            .title {
                font-size: 16rem;
                font-weight: 600;
                margin-bottom: 5rem;
            }
            `}</style>
        </>
    );
}

export default function TitlePriceBadge({editCartItem,product,addToCartActionsRef,price,onReadMoreReviewsClick = () => {},alreadyAddedToCart,alreadyAlreadyAddedToCart,enabled,noticeText}:TitlePriceBadgeProps)
{
    const cssStateManager = useCssStateManager(["title-price-badge"]);
    cssStateManager.useProperty(!enabled,"disabled");
    cssStateManager.useProperty(!!editCartItem,"is-editing");

    const hideReview = useSmartState(false);

    const { reviewsCount,starRating } = consumeReviewsHeader(product.id);
    const discountedPrice = prebuildData.getProductPageDiscountedPrice(product.id,price).price;

    const { productPriceTitleBadge: { backgroundColor,textColor,borderColor,optionsReadyBorderColor,priceTextColor,reviewCountColor } } = prebuildData.getActiveTheme();

    const teaseCaption = ((product.visibility==="tease") && (product.teaseCaption))?product.teaseCaption:"";

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <div className="outline"></div>
                <div className="price">
                    <div className="price-display">
                        <PriceDisplay price={discountedPrice} size={40} />
                    </div>
                    <div className="price-update">
                        <div className="price-update-inner">
                            <PriceUpdate price={discountedPrice} />
                        </div>
                    </div>
                </div>
                <div className="title">
                    {product.caption}
                </div>                
                {(reviewsCount!==-1)?(<>
                    <div className="rating">
                        <StarRating rating={starRating} scale={1.3} />
                    </div>
                    <div className="review_count">
                        {reviewsCount>0?<>
                            {reviewsCount} review{(reviewsCount===1)?"":"s"}
                        </>:"No reviews yet"}                    
                    </div>
                </>):<div className="awaiting-review-header">---</div>}
                {((reviewsCount>0) && (!hideReview.value) && (!editCartItem))?(<>
                    <Separator enabled={enabled} />
                    <ReviewHighlight product={product} onReadMoreReviewsClick={onReadMoreReviewsClick} enabled={enabled} hideReview={hideReview} />
                </>):""}
                <div className="edit-status-wrapper">
                    <Separator enabled={enabled} />
                    <div className="edit-status">EDITING</div>                    
                </div>
                <Separator enabled={enabled} />
                <div className="add-to-cart">
                    <AddToCart 
                        editCartItem={editCartItem}
                        inBadge={true}
                        added={alreadyAddedToCart}
                        already={alreadyAlreadyAddedToCart}
                        onClick={addToCartActionsRef.current.onClick}
                        onViewInCart={addToCartActionsRef.current.onViewInCart}
                        onSaveClick={addToCartActionsRef.current.onSaveClick}
                        teaseCaption={teaseCaption}
                    />
                    <div className="blocker"></div>
                </div>
                <Includes 
                    product={product} 
                    enabled={enabled} 
                />                
            </div>
            <Notice text={noticeText} />

            <style jsx>{`
            .awaiting-review-header {

            }

            .edit-status-wrapper {
                display: none;
            }

            .is-editing .edit-status-wrapper {
                display: block;
            }

            .edit-status {
                background-color: #888;
                display: inline-block;
                color: #eee;
                padding: 10rem 30rem;
                font-weight: 600;
                margin-top: -5rem;
            }

            .title-price-badge {
                --border-radius: 15rem; 
                position: relative;
                width: 350rem;
                border-radius: var(--border-radius);
                border: 1rem solid ${borderColor};
                padding: 30rem;
                color: ${textColor};
                text-align: center;
                --duration: 0.6s;
                background-color: ${backgroundColor};
                transition: background-color var(--duration);
            }
            .outline {
                --padding: -1rem;
                position: absolute;
                left: var(--padding);
                top: var(--padding);
                bottom: var(--padding);
                right: var(--padding);
                border: 1rem solid ${optionsReadyBorderColor};
                border-radius: var(--border-radius);
                opacity: 1;
                transition: opacity var(--duration);
            }
            .disabled .outline {
                opacity: 0;
            }

            .title {
                font-size: 20rem;
                margin-bottom: 10rem;
            }
            .price {
                margin-bottom: 10rem;
                color: ${priceTextColor};
            }
            .rating {
                margin-bottom: 10rem;
            }
            .review_count{
                color: ${reviewCountColor};
            }
            .price-display,.price-update {
                display: inline-block;
            }
            .price-update {
                position: relative;
            }
            .price-update-inner {
                position: absolute;
                top: -7rem;
                left: 10rem;
            }
            .add-to-cart {
                position: relative;
                padding-top: 12rem;
                padding-bottom: 14rem;
                opacity: 1;
                transition: opacity var(--duration);
            }
            .disabled .add-to-cart {
                opacity: 0.32;
            }

            .disabled {
                /* background-color: transparent; */
                background-color: #fff;
            }

            .add-to-cart .blocker {
                position: absolute;
                left: 0rem;
                right: 0rem;
                top: 0rem;
                bottom: 0rem;
                display: none;
            }

            .disabled .add-to-cart .blocker {
                display: block;
            }
            `}</style>
        </>
    );
}