import { CSSProperties, useContext, useEffect, useRef } from "react";
import { LayoutStateContext } from "../../contexts/layout_state";
import { WindowSizeContext } from "../../contexts/window_size";
import { useCssStateManager } from "../../hooks/use_css_state_manager";
import { useSmartState } from "../../hooks/use_smart_state";
import { prebuildData } from "../../lib/prebuild-data";
import { EventPane } from "../core/event_pane";


export type NavigationArrowProps = {
    side: "left" | "right";
    size: number;
    iconSize: number;
    onClick?: () => void;
};

function NavigationArrow({side,size,iconSize,onClick}:NavigationArrowProps)
{
    const cssStateManager = useCssStateManager(["navigation-arrow"]);
    cssStateManager.useProperty(side==="right","right");

    const styles: CSSProperties = {
        height: `${size}rem`,
        fontSize: `${iconSize}rem`
    };

    return (
        <>
            <div className={cssStateManager.getClassNames()} style={styles}>
                <div className="icon-box hori-verti-centered">
                    <i className={`icon icon-navbar-${side}-arrow`} />
                </div>
                <div className="fade-gradient"></div>                
                <EventPane onClick={onClick} zIndex={3} />
            </div>
            <style jsx>{`
            .navigation-arrow {
                --duration: 0.3s;
                color: #aaa;
                display: flex;
                flex-direction: row;
                position: relative;
            }

            .navigation-arrow.right {
                flex-direction: row-reverse;
            }

            .icon-box {
                position: relative;
                height: 100%;
                width: 24rem;
                z-index: 2;
            }

            .icon {
                position: relative;
                z-index: 2;
            }

            .fade-gradient {
                --shift: -24rem;
                position: relative;
                height: 100%;
                width: 30rem;
                background: linear-gradient(90deg, #fbfbfd 0%, rgba(0,0,0,0) 100%);
                left: var(--shift);
                z-index: 1;
            }

            .right .fade-gradient {
                background: linear-gradient(90deg, rgba(0,0,0,0) 0%, #fbfbfd 100%);

                left: calc(var(--shift) * -1);
            }
            `}</style>
        </>
    );
}

export type TabsHeaderDisplayStyle = "relaxed" | "tight" | "full";

export type TabsHeaderProps = {
    value: number;
    onChange?: (newValue: number) => void;
    captions: string[];
    displayStyle: TabsHeaderDisplayStyle;
};

export default function TabsHeader({value,onChange = () => {},captions,displayStyle}:TabsHeaderProps)
{
    const cssStateManager = useCssStateManager(["tabs-header"]);
    cssStateManager.useProperty(displayStyle==="relaxed","relaxed");    
    cssStateManager.useProperty(displayStyle==="full","full");
    const hasScroll = cssStateManager.useState(false,"has-scroll");    
    const { scalePixelToRem,scaleRemToPixel,mobileDetect: { isActive: isMobile } } = useContext(LayoutStateContext);
    const windowSize = useContext(WindowSizeContext);

    const headersRef = useRef<HTMLDivElement>();
    const headerWidthsRef = useRef(captions.map((_,index) => 0));
    const { current: headerWidths } = headerWidthsRef;

    const showLeftNavigator = cssStateManager.useState(false,"show-left-navigator");
    const showRightNavigator = cssStateManager.useState(false,"show-right-navigator");

    const scrollerRef = useRef<HTMLDivElement>();    

    const scrollHandler = (updateHasScroll = false) => {
        if (scrollerRef.current) {
            const { current: scrollerElem } = scrollerRef;
            const availableScroll = scrollerElem.scrollWidth-scrollerElem.offsetWidth;
            if (updateHasScroll) {
                hasScroll.value = (availableScroll>0);
            }
            if ((availableScroll>0) && !isMobile.current) {
                showLeftNavigator.value = (scrollerElem.scrollLeft>0);
                showRightNavigator.value = (scrollerElem.scrollLeft<availableScroll);
            }
            else {
                showLeftNavigator.value = showRightNavigator.value = false;
            }    
        };
    };

    const navigatorClickHandler = (side: "left" | "right") => {
        if (scrollerRef.current) {
            const { current: scrollerElem } = scrollerRef;
            if (side==="left") {
                scrollerElem.scrollTo({left: 0,behavior: "smooth"});
            }
            else {
                const availableScroll = scrollerElem.scrollWidth-scrollerElem.offsetWidth;
                scrollerElem.scrollTo({left: availableScroll,behavior: "smooth"});
            }
        }        
    };

    useEffect(() => {
        scrollHandler(true);
    },[windowSize.width]);    

    const getHeaderOffsetLeft = (value: number) => headerWidths.filter((_,index) => (index<value)).reduce((acc,inc) => acc+inc,0);

    useEffect(() => {
        if (scrollerRef.current) {
            const { current: scrollerElem } = scrollerRef;
            const availableScroll = scrollerElem.scrollWidth-scrollerElem.offsetWidth;
            const headerOffset = scaleRemToPixel(getHeaderOffsetLeft(value));
            const centeredScrollLeft = headerOffset-((scrollerElem.offsetWidth-headerWidths[value])/2);
            const targetScrollLeft = Math.min(availableScroll,Math.max(0,centeredScrollLeft));
            if (scrollerElem.scrollLeft!==targetScrollLeft) {
                scrollerElem.scrollTo({left: targetScrollLeft,behavior: "smooth"});
            }
        }        
    },[value]);

    useEffect(() => {
        if (headersRef.current) {
            const { current: headersElem } = headersRef;
            headerWidthsRef.current = captions.map((_,index) => {
                const headerElem = headersElem.querySelector(`.column${index}`) as HTMLDivElement;
                return headerElem?scalePixelToRem(headerElem.offsetWidth):0;
            });
        }
    },[]);

    const selectorLineStyles: CSSProperties = {
        width: `${headerWidths[value]}rem`,
        transform: `translateX(${getHeaderOffsetLeft(value)}rem)`
    };

    const navigatorProps = {
        size: 52,
        iconSize: 20
    };

    const { productDescriptionTabs: { hoverTextColor,selectedColor,textColor } } = prebuildData.getActiveTheme();

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <div className="hider">
                    <div ref={scrollerRef} className="scroller" onScroll={() => scrollHandler()}>
                        <div className="scroller-inner">
                            <div ref={headersRef} className="headers">
                                {captions.map((caption,index) => (
                                    <div key={index} className={`header column${index}${(index===value)?" selected":""}`}>
                                        <span dangerouslySetInnerHTML={{__html: caption.split(/(&[^;]+;)/).map((value) => /^&[^;]+;$/.test(value)?value:value.toUpperCase()).join("")}}></span>
                                        <EventPane onClick={() => { if (index!==value) onChange(index); }} hoverOnTouchDown />
                                    </div>
                                ))}                    
                            </div>
                            <div className="line"></div>
                            <div className="selector-line" style={selectorLineStyles}></div>
                        </div>
                    </div>
                </div>
                <div className="navigator left"><NavigationArrow side="left" {...navigatorProps} onClick={() => navigatorClickHandler("left")} /></div>
                <div className="navigator right"><NavigationArrow side="right" {...navigatorProps} onClick={() => navigatorClickHandler("right")} /></div>
            </div>
            <style jsx>{`
            .tabs-header {
                --duration: 0.4s;                                                
                position: relative;
            }

            .hider {
                overflow: hidden;
                height: 54rem;
            }

            .has-scroll .scroller-inner {
                display: inline-block;
            }

            .navigator {
                --duration: 0.3s;
                --hori: -17rem;
                position: absolute;
                top: -5rem;
                visibility: hidden;
                opacity: 0;
                transition: visibility var(--duration),opacity var(--duration);
            }

            .navigator.left {
                left: var(--hori);
            }

            .show-left-navigator .navigator.left {
                visibility: visible;
                opacity: 1;
            }

            .navigator.right {
                right: var(--hori);
            }

            .show-right-navigator .navigator.right {
                visibility: visible;
                opacity: 1;
            }

            .scroller {
                overflow-x: auto;
                padding-bottom: 50rem;
            }

            .headers {
                display: flex;
            }

            .header {
                padding: 12rem 14rem;
                font-size: 14rem;
                font-weight: 600;
                letter-spacing: 0.5rem;
                color: ${textColor};
                position: relative;
                transition: color var(--duration);
                white-space: nowrap;
            }

            .full .header {
                flex: 1 0;
                text-align: center;
                padding: 12rem 12rem;
            }

            .header.hover {
                color: ${hoverTextColor};
            }

            .relaxed .header {
                padding: 12rem 12rem;
            }

            .header.selected {
                color: ${selectedColor};
            }

            .line {
                height: 1rem;
                background-color: #ddd;
                margin-top: 10rem;
                width: 100%;
            }

            .selector-line {
                height: 2rem;
                background-color: ${selectedColor};
                position: relative;
                top: -3rem;
                transition: transform var(--duration),width var(--duration);
            }
            `}</style>
        </>
    );
}