const useExpandable = (closedHeight: number,onBeforeShowLess = () => {}) => {
    const innerOffsetHeight = useSmartState(0);
    const innerRef = useResizeObserver(({}) => {
        if (innerRef.current) {
            innerOffsetHeight.value = scalePixelToRem(innerRef.current.offsetHeight);
        }        
    });
    const initialHeightAssessedRef = useRef(false);
    const transitionDurationRef = useRef(0);
    const isOpen = useSmartState(false);
    const isDisabled = useSmartState(false);
    const { scalePixelToRem,scaleFactorDetectUseEffect } = useContext(LayoutStateContext);

    const calculateTransitionDuration = () => {
        const durationPer100 = 0.13;
        return Math.min(((Math.abs(innerOffsetHeight.value-closedHeight)/100)*durationPer100),1);
    };

    const clickHandler = () => {
        if (isOpen.value) {
            onBeforeShowLess();
            isOpen.value = false;
        }
        else {
            if (innerRef.current) {
                innerOffsetHeight.value = scalePixelToRem(innerRef.current.offsetHeight);
                transitionDurationRef.current = calculateTransitionDuration();
                isOpen.value = true;
            }
        }
    };

    const style: CSSProperties = {};
    const controllerStyle: CSSProperties = {};
    const fadeStyle: CSSProperties = {};
    if (isDisabled.value) {
        style.height = "auto";
        style.transition = "none";
    }
    else {
        if (initialHeightAssessedRef.current) {
            style.height = `${isOpen.value?innerOffsetHeight.value:closedHeight}rem`;
            style.transition = `height ${transitionDurationRef.current}s`;        
        }
        else {
            style.height = "0rem";
            style.transition = "none";
        }
    }
    fadeStyle.opacity = isOpen.value?0:1;
    fadeStyle.transition = `opacity ${transitionDurationRef.current}s`;
    const caption = isOpen.value?"Show Less":"Show More";
    const directionClassName = isOpen.value?"up":"down";

    if (isDisabled.value) {
        controllerStyle.display = "none";
    }

    scaleFactorDetectUseEffect((scalePixelToRem) => {
        if (innerRef.current) {
            if (scalePixelToRem(innerRef.current.offsetHeight)<=closedHeight*1.25) {
                isDisabled.value = true;
            }
        }
        initialHeightAssessedRef.current = true;
    },[]);

    return { innerRef,clickHandler,style,caption,directionClassName,fadeStyle,controllerStyle,disabled: isDisabled.value };
};

