import { Fragment, useEffect, useRef } from "react";
import { useCssStateManager } from "../../hooks/use_css_state_manager";
import { useResizeObserver } from "../../hooks/use_resize_observer";
import { useSmartState } from "../../hooks/use_smart_state";
import { prebuildData } from "../../lib/prebuild-data";
import SvgButton from "../core/svg_button";

export type VerticalSliderProps = {
    children: React.ReactNode[];
};

export default function VerticalSlider({children}:VerticalSliderProps)
{
    if (!Array.isArray(children)) children = [children];
    const cssStateManager = useCssStateManager(["vertical-slider"]);

    // const itemWidth = useSmartState(200);
    const targetItemWidth = 163;
    const itemWidth = useSmartState(targetItemWidth);
    const spacer = useSmartState(0);
    const minimumSpacer = 40;

    const viewIndexRef = useRef(0);
    const itemsPerPageRef = useRef(0);
    const isTransitioningRef = useRef(false);
    const scrollLeft = useSmartState(0);
    const scrollTransitionDuration = useSmartState(0);

    const refOnResize = useResizeObserver(({width}) => {
        // const itemsPerPage = 6;
        let remainder = width%targetItemWidth;
        let times = Math.round((width-remainder)/targetItemWidth);
        if (remainder===0) {
            times--;
            remainder = targetItemWidth;
        }
        let potSpacer = 0;
        if (times>1) {
            potSpacer = remainder/(times-1);
            while((times>1) && (potSpacer<minimumSpacer))
            {
                times--;
                remainder = width-times*targetItemWidth;
                potSpacer = (times>1)?(remainder/(times-1)):0;
            }
        }
        if (times===1) {
            itemWidth.value = width;
            spacer.value = 0;
        }
        else {
            itemWidth.value = targetItemWidth;
            spacer.value = potSpacer;
        }
        itemsPerPageRef.current = times;

        const maxIndex = children.length-itemsPerPageRef.current;
        let targetIndex = Math.min(viewIndexRef.current,maxIndex);
        const newScrollLeft = getScrollLeftFromIndex(targetIndex);
        if (newScrollLeft!==scrollLeft.value) {
            scrollLeft.value = newScrollLeft;
            scrollTransitionDuration.value = 0;
            isTransitioningRef.current = false;
        }
    });

    const sliderButtonHeight = 34;
    const sliderButtonPadding = 15;
    const sliderButtonOpacity = 0.2;
    const sliderButtonOpacityHoverFactor = 1.45;
    const sliderButtonOuterShift = 60;

    const transitionPixelsPerSecond = 813.6/0.8;    

    const getScrollLeftFromIndex = (index: number) => (itemWidth.value+spacer.value)*index;
    const getScrollDistanceBetweenIndexes = (index1: number,index2: number) => Math.abs(getScrollLeftFromIndex(index1)-getScrollLeftFromIndex(index2));

    const slideToViewIndex = (index: number) => {
        if (index===viewIndexRef.current) return;
        const maxIndex = children.length-itemsPerPageRef.current;
        if ((index<0) || (index>maxIndex)) return;
        const toScrollLeft = getScrollLeftFromIndex(index);
        const distance = getScrollDistanceBetweenIndexes(viewIndexRef.current,index);
        isTransitioningRef.current = true;
        viewIndexRef.current = index;
        scrollLeft.value = toScrollLeft;        
        scrollTransitionDuration.value = distance/transitionPixelsPerSecond;         
    };

    const slideToPrevious = () => {
        if (isTransitioningRef.current) return;
        const toIndex = Math.max(viewIndexRef.current-(itemsPerPageRef.current-1),0);
        slideToViewIndex(toIndex);
    };
    const slideToNext = () => {
        if (isTransitioningRef.current) return;
        const maxIndex = children.length-itemsPerPageRef.current;
        const toIndex = Math.min(viewIndexRef.current+itemsPerPageRef.current-1,maxIndex);        
        slideToViewIndex(toIndex);
    };

    const maxIndex = children.length-itemsPerPageRef.current;

    cssStateManager.useProperty(viewIndexRef.current===0,"hide-slider-left-button");
    cssStateManager.useProperty(viewIndexRef.current===maxIndex,"hide-slider-right-button");

    const sliderRef = useRef<HTMLDivElement>();

    const transitionEndHandler = (elem: HTMLDivElement,propertyName: string) => {
        if ((elem===sliderRef.current) && (propertyName==="transform")) {
            isTransitioningRef.current = false;
        }
    };

    return (
        <>
            <div ref={refOnResize} className={cssStateManager.getClassNames()}>
                <div className="overflow-hidden-wrapper">
                    <div
                        ref={sliderRef}
                        className="slider"
                        style={{
                            transform: `translateX(-${scrollLeft.value}rem)`,
                            transition: `transform ${scrollTransitionDuration.value}s`
                        }}
                        onTransitionEnd={(e) => transitionEndHandler(e.target as HTMLDivElement,e.propertyName)}
                    >
                        {children.map((element,index) => (
                            <div 
                                className={`item ${(index===children.length-1)?"last":""}`} 
                                key={index}
                                style={{
                                    flex: `0 0 ${itemWidth.value}rem`,
                                    marginRight: `${(index<children.length-1)?spacer.value:0}rem`
                                }}
                            >
                                {element}
                            </div>
                        ))}
                    </div>
                </div>
                <div className="slider-button slider-button-left">
                    <SvgButton 
                        src={prebuildData.bustCache("/images/product/gallery/left-arrow.svg")} 
                        aspectRatio={{width: 120,height: 188}} 
                        height={sliderButtonHeight}
                        paddingHori={sliderButtonPadding}
                        paddingVert={sliderButtonPadding}
                        opacity={sliderButtonOpacity}
                        opacityHover={sliderButtonOpacity*sliderButtonOpacityHoverFactor}
                        onClick={slideToPrevious}
                    />
                </div>
                <div className="slider-button slider-button-right">
                    <SvgButton 
                        src={prebuildData.bustCache("/images/product/gallery/right-arrow.svg")} 
                        aspectRatio={{width: 120,height: 188}} 
                        height={sliderButtonHeight}
                        paddingHori={sliderButtonPadding}
                        paddingVert={sliderButtonPadding}
                        opacity={sliderButtonOpacity}
                        opacityHover={sliderButtonOpacity*sliderButtonOpacityHoverFactor}
                        onClick={slideToNext}
                    />
                </div>
            </div>
            <style jsx>{`
            .vertical-slider {                
                position: relative;
            }

            .overflow-hidden-wrapper {
                overflow-x: hidden;
                width: 100%;
                height: 100%;
            }

            .slider {
                display: flex;
            }

            .item {
            }
            .item.last {
            }

            .slider-button {                
                position: absolute;
                top: calc(32.5% - ${(sliderButtonHeight+sliderButtonPadding*2)/2}rem);                
            }
            .slider-button-left {
                left: -${sliderButtonOuterShift}rem;
            }
            .slider-button-right {
                right: -${sliderButtonOuterShift}rem;
            }

            .hide-slider-left-button .slider-button-left,.hide-slider-right-button .slider-button-right {
                display: none;
            }

            `}</style>
        </>
    );
}