import { useContext, useEffect } from "react";
import { LayoutStateContext } from "../../../../contexts/layout_state";
import { useCssStateManager } from "../../../../hooks/use_css_state_manager";
import { useSmartState } from "../../../../hooks/use_smart_state";
import { prebuildData } from "../../../../lib/prebuild-data";
import { OptionValueProductSelect } from "../../../../lib/services/cart/types";
import OptionTitle from "./commons/title";

export type MultiSelectOptionProps = {
    title: string;
    subTitle?: string;
    input: MultiSelectInput;
    onValue?: (value: OptionValueProductSelect,auto?: boolean) => void;
    loadFromSavedValue?: OptionValueProductSelect;
};

export type MultiSelectEntry = {
    caption: string;
    value: string;
};

export type MultiSelectInput = {
    min: number;
    max: number;
    showAll: boolean;
    captions: string[];
    entries: MultiSelectEntry[];
};

type SingleSelectProps = {
    value: string;
    onChange?: (value: string) => void;
    caption: string;
    entries: MultiSelectEntry[];
    required: boolean;
    show: boolean;
    fadeDuration: number;
};

const unselectedValue = "--unselected--";
const removeValue = "--remove--";

function SingleSelect({
        caption,
        entries,
        required,
        value,
        onChange = () => {},
        show,
        fadeDuration,
    }: SingleSelectProps) 
{
    const { mobileDetect } = useContext(LayoutStateContext);
    const { smallDesktopWidthCutoff: mobileCutoff } = mobileDetect;
    
    const cssStateManager = useCssStateManager(["container"]);
    const unselected = value===unselectedValue;
    cssStateManager.useProperty(unselected,"unselected");
    cssStateManager.useProperty(show,"show");

    const { productOptions: { buttonUnselectedTextColor,buttonSelectedTextColor,buttonBackgroundColor } } = prebuildData.getActiveTheme();

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <select className="select" onChange={(e) => onChange(e.target.value)} value={value}>
                    <option className="unselected-option" value={unselectedValue}>{caption}{required?"":" (optional)"}</option>
                    {(!required && !unselected)?<option className="remove-option" value={removeValue}>-- Remove ---</option>:""}                    
                    {entries.map(({caption,value},index) => <option key={index} value={value}>{caption}</option>)}
                </select>
            </div>
            <style jsx>{`
            .container {
                margin-bottom: 20rem;
                opacity: 0;
                background-color: ${buttonBackgroundColor};
                transition: opacity ${fadeDuration}s;
            }
            .container.show {
                opacity: 1;
            }
            .select {
                cursor: pointer;
                height: 45rem;
                outline: none;
                border: 1rem solid rgba(190, 190, 190);
                border-radius: 5rem;
                width: 100%;
                font-size: 16rem;
                font-family: "Open Sans", "Arial", "Helvetica", sans-serif;
                padding: 0rem 9rem;
                color: ${buttonSelectedTextColor};
                background-color: transparent;
                font-weight: 600;                            
            }
            .unselected .select {
                color: ${buttonUnselectedTextColor};
            }
            .select option {
                color: ${buttonSelectedTextColor};
            }
            .unselected-option {
                display: none;
            }

            @media screen and (max-width: ${mobileCutoff}px) {
                .select {
                    background-color: var(--body-background-color);
                }
            }
            `}</style>
        </>
    );
}

export default function MultiSelectOption({
        title,
        input,
        subTitle = "",
        onValue = () => {},
        loadFromSavedValue
    }:MultiSelectOptionProps)
{    
    const cssStateManager = useCssStateManager(["container"]);
    const values = useSmartState<string[]>([]);
    const selectHeight = 45;
    const spacing = 20;
    const fadeDuration = 0.3;

    const selectsCount = input.showAll?input.max:Math.max(input.min,Math.min(input.max,values.value.length+1));
    const selectsHeight = selectHeight+(selectHeight+spacing)*(selectsCount-1);

    useEffect(() => {
        if (loadFromSavedValue) {
            updateValue(loadFromSavedValue.ids,true);
        }
    },[loadFromSavedValue]);

    const updateValue = (ids: string[],auto?: boolean) => {
        const newValue = [...ids];
        values.value = newValue;
        onValue({
            type: "product-select",
            ids: newValue
        },auto);
    };

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <OptionTitle caption={title} subCaption={subTitle} />
                <div className="selects" style={{height: `${selectsHeight}rem`}}>
                    {Array(input.max).fill(0).map((_,index) => {
                        return <SingleSelect 
                            key={index} 
                            caption={input.captions[index]} 
                            entries={input.entries} 
                            required={index<input.min}
                            value={(index<values.value.length)?values.value[index]:unselectedValue}
                            onChange={(value) => {
                                if (value===removeValue) {
                                    values.value.splice(index,1);
                                }
                                else {
                                    if (index<values.value.length) {
                                        values.value[index] = value;
                                    }
                                    else {
                                        values.value.push(value);
                                    }                                
                                }
                                updateValue(values.value);
                            }}
                            show={index<selectsCount}
                            fadeDuration={fadeDuration}
                        />;
                    })}
                </div>
            </div>
            <style jsx>{`
            .container {
                padding-bottom: 16rem;
            }
            .select {
                cursor: pointer;
                height: ${selectHeight}rem;
                outline: none;
                border: 1rem solid rgba(190, 190, 190);
                border-radius: 5rem;
                width: 100%;
                font-size: 16rem;
                font-family: "Open Sans", "Arial", "Helvetica", sans-serif;
                padding: 0rem 9rem;
                color: #6c6c6c;
                background-color: transparent;
                font-weight: 600;            
                margin-bottom: ${spacing}rem;
            }
            .selects {
                overflow-y: hidden;
                transition: height ${fadeDuration}s;
            }
            `}</style>
        </>
    );
}