import { useCssStateManager } from "../../../hooks/use_css_state_manager";
import { QuantitySelect,QuantityValue } from "./options/select-with-quantity";
import { useSmartState } from "../../../hooks/use_smart_state";
import { EventPane } from "../../core/event_pane";
import { MutableRefObject, useContext, useEffect, useRef } from "react";
import { usePubSubRequest } from "../../../hooks/use_pub_sub_request";
import { misc } from "../../../lib/misc";
import { LayoutStateContext } from "../../../contexts/layout_state";
import { StarRatingProps } from "../../common/star_rating";
import { Notice } from "./title-price-badge";
import { CartItem } from "../../../lib/services/cart/types";
import { prebuildData } from "../../../lib/prebuild-data";

export type AddToCartProps = {
    editCartItem?: CartItem;
    onClick?: (quantity: number) => void;
    onSaveClick?: () => void;
    onViewInCart?: () => void;
    added?: boolean;
    already?: boolean;
    containerRef?: MutableRefObject<HTMLDivElement>;
    inBadge?: boolean;
    descriptionHasTabs?: boolean;
    sliderIsOpen?: boolean;
    noticeText?: string;
    teaseCaption: string;
};

export default function AddToCart({editCartItem,onClick = () => {},onSaveClick = () => {},onViewInCart = () => {},added = false,already = false,containerRef,inBadge,descriptionHasTabs = false,sliderIsOpen = false,noticeText,teaseCaption}:AddToCartProps)
{
    const { mobileDetect } = useContext(LayoutStateContext);
    const { smallDesktopWidthCutoff: mobileCutoff } = mobileDetect;
    const cssStateManager = useCssStateManager(["container"]);
    cssStateManager.useProperty(teaseCaption!=="","has-tease");
    cssStateManager.useProperty(descriptionHasTabs,"description-has-tabs");
    cssStateManager.useProperty(sliderIsOpen,"slider-is-open");
    cssStateManager.useProperty(added,"added");
    cssStateManager.useProperty(inBadge,"in-badge");
    cssStateManager.useProperty(!!editCartItem,"is-editing");

    const initialQuantityValue = {
        custom: false,
        value: 1,
        valueDisplay: ""
    };
    const quantityValue = useSmartState<QuantityValue>(initialQuantityValue);

    const clickHandler = () => {
        onClick(quantityValue.value.value);
    };

    useEffect(() => {
        if (added) {
            quantityValue.value = {...initialQuantityValue};
        }
    },[added]);

    const ref = containerRef || useRef<HTMLDivElement>();

    usePubSubRequest("add_to_cart_offset_top",async () => {
        if (ref.current) {
            return misc.getOffsetRelativeToParent(ref.current,".page").top;
        }
        else {
            return null;
        }
    });

    const { productPriceTitleBadge: { buttonColor,buttonTextColor } } = prebuildData.getActiveTheme();

    return (
        <>
            <div className={cssStateManager.getClassNames()} ref={ref}>
                <div className="teasing">
                    <div className="tease-button">{teaseCaption}</div>                    
                </div>
                <div className="editing">
                    <div className="button">
                        Save
                        <EventPane onClick={onSaveClick} hoverOnTouchDown />
                    </div>
                </div>
                <div className="controls">
                    <div className="quantity-wrapper">
                        <QuantitySelect value={quantityValue} excludeZero={true} inBadge={inBadge} />
                    </div>                
                    <div className="button">
                        Add To Cart
                        <EventPane onClick={clickHandler} hoverOnTouchDown />
                    </div>
                </div>
                <div className="added-to-cart">
                    <div className="tick"></div>
                    <div className="in-cart-notice">
                        {already?"Already Added":"Added To Cart"}                        
                    </div>
                    <div className="mobile-spacer"></div>
                    <div className="view-in-cart-button">
                        View in Cart <div className="arrow"><span></span></div>
                        <EventPane onClick={onViewInCart} hoverOnTouchDown />
                    </div>
                </div>
                <Notice text={noticeText} />
            </div>
            <style jsx>{`
            .container {     
                --border-color: #ddd;           
                margin-top: 55rem;
                border-top: 1rem solid var(--border-color);
                border-bottom: 1rem solid var(--border-color);
                padding: 20rem 0rem;
            }
            .teasing {
                display: none;
                justify-content: center;
            }

            .has-tease .teasing {
                display: flex;                
            }

            .tease-button {
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 14rem;
                background-color: #1d8dff;
                /* background-color: #1776d5; */
                color: ${buttonTextColor};
                padding: 11rem 32rem;
                border-radius: 25rem;
                /* margin-left: 23rem; */
                font-weight: 600;
                position: relative;
                top: 0rem;            
                position: relative;
                opacity: 0.5;
                transition: opacity 0.3s;
                white-space: nowrap;
            }

            .in-badge {
                border-top: none;
                border-bottom: none;
                margin-top: 0rem;
                padding: 0rem;
            }
            .controls {
                display: flex;
            }
            .in-badge .controls {
                justify-content: center;
            }
            .added .controls {
                display: none;
            }
            .added-to-cart {
                display: none;
                color: #999999;
                font-size: 20rem;
                font-weight: 600;   
                position: relative;
                height: 45rem;
                align-items: center;
            }
            .in-badge .added-to-cart {
                font-size: 16rem;
                justify-content: center;
            }
            .added .added-to-cart {
                display: flex;
            }

            .added-to-cart .tick {
                color: #00a300;
                font-size: 28rem;
                font-weight: 600;
            }

            .quantity-wrapper {
                flex: 0 0 80rem;
            }
            .button {
                position: relative;
                height: 45rem;
                background-color: ${buttonColor};
                /* background-color: #1776d5; */
                color: ${buttonTextColor};
                border-radius: 5rem;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                padding: 0rem 25rem;
                margin-left: 15rem;
                cursor: pointer;
                opacity: 1;
                transition: opacity 0.3s;                
            }
            .button.hover {
                opacity: 0.9;
            }
            .in-cart-notice {
                display: inline-block;
                width: 198rem;
                padding-left: 10rem;
            }
            .in-badge .in-cart-notice {
                width: auto;
                margin-right: 14rem;
            }

            .in-badge .added-to-cart .tick {
                display: block;
                font-size: 22rem;
                margin-right: -4rem;
            }

            .view-in-cart-button {
                display: flex;
                align-items: center;
                font-size: 14rem;
                background-color: ${buttonColor};
                /* background-color: #1776d5; */
                color: ${buttonTextColor};
                padding: 11rem 22rem;
                padding-right: 32rem;
                border-radius: 25rem;
                /* margin-left: 23rem; */
                font-weight: 600;
                position: relative;
                top: 0rem;            
                position: relative;
                opacity: 1;
                transition: opacity 0.3s;
                white-space: nowrap;
            }
            .view-in-cart-button.hover {
                opacity: 0.9;
            }

            .editing {
                padding: 0rem 60rem;
                display: none;
            }

            .editing .button {
                margin-left: 0rem;
            }            

            .arrow {
                position: absolute;
                right: 10rem;
                top: calc(50% - 13rem);
                font-size: 17rem;
                overflow: hidden;
            }
            .arrow span {
                position: relative;
                left: -5rem;
            }
            .mobile-spacer {
                display: none;
            }

            .is-editing .editing {
                display: block;
            }

            .is-editing .controls,.is-editing .added-to-cart {
                display: none;
            }

            .has-tease .controls,.has-tease .added-to-cart,.has-tease .editing {
                display: none;
            }

            @media screen and (max-width: ${mobileCutoff}px) {
                .controls {
                    justify-content: space-between;
                    padding: 0rem 5rem;
                }        

                .added-to-cart {
                    font-size: 16rem;
                    padding: 0rem 5rem;
                }

                .in-cart-notice {
                    width: auto;
                }

                .mobile-spacer {
                    display: block;
                    flex: 1 1;
                    min-width: 15rem;
                }

                .container {
                    margin-top: 5rem;
                }

                .description-has-tabs.container {
                    padding-bottom: 50rem;
                }

                .slider-is-open.container {
                    border-top: none;
                }
            }            
            `}</style>
        </>
    );
}